export const docketNames = ['Bluedart', 'FedEx'];

export const docketNamesForLocal = ['Bluedart', 'Cityxfer', 'FedEx'];

export const getShippingPartner = () => {
    return ShippingPartner;
}

export const getDocketNames = (city) => {
    return ["bangalore", "bengaluru"].includes(city.toLocaleLowerCase()) ? docketNamesForLocal : docketNames;
}

export const getShippingPartnerName = () => {
    // return ShippingPartner.filter(partner => partner.partnerName !== "" && partner.partnerName).reduce((a, b) => a !== b);
    return ShippingPartner.filter(partner => partner.partnerName !== "" && partner.partnerName).map(a => a.partnerName)
}

export const getDistinctShippingPartnerName = () => {
    let filtered = getShippingPartnerName()
    return filtered.filter((item, index) => filtered.indexOf(item) === index)
}

export const getShippingPartnerId = (id) => {
    let shippingPartner = ShippingPartner.filter(status => status.id === id);
    return shippingPartner.length > 0 ? shippingPartner[0] : {};
}

export const getWareHouse = () => {
    return warehouses;
}

export const getActiveWareHouse = () => {
    return warehouses.filter(warehouse => warehouse.is_active === 1);
}

export const getWareHouseById = (id) => {
    let warehouse = warehouses.filter(warehouse => warehouse.id === id);
    return warehouse.length > 0 ? warehouse[0] : {};
}
