import { GET_REFUND_ORDERS_SUCCESS, RESET_REFUND_ORDERS_DATA, EDIT_REFUND_ORDERS_SUCCESS } from "./constants";

const initialState = {
    financeData: [],
    refreshData: false
};

export default function financeReducer(state = initialState, action) {
    switch (action.type) {
        case GET_REFUND_ORDERS_SUCCESS:
            return Object.assign({}, state, {
                financeData: action.data,
                refreshData: false
            });

        case RESET_REFUND_ORDERS_DATA:
            return Object.assign({}, state, {
                financeData: [],
                refreshData: false
            });

        case EDIT_REFUND_ORDERS_SUCCESS:
            return Object.assign({}, state, {
                refreshData: true
            });

        default: return state;
    }
}