import React, { Component } from 'react';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';
import './style.scss';
import PageHeader from '../../components/PageHeader';
import { getActivitiesAction, resetActivitiesAction } from './action';
import { toggleUserStatusAction } from '../../components/ToggleUserStatus/action';
import { getTime } from '../../config/time';

class Activity extends Component {

    state = {
        activityTypes: [{
            id: 2,
            label: "Working",
            key: "working"
        }, {
            id: 1,
            label: "Break",
            key: "break"
            // }, {
            //     id: 0,
            //     label: "Stop",
            //     key: "stop"
        }],
        activeStatus: "",
    }

    componentDidMount() {
        this.props.getActivitiesAction();
    }

    componentWillUnmount() {
        this.props.resetActivitiesAction();
    }

    componentWillReceiveProps(props) {
        let { activeStatus } = this.state;
        let { userDetails } = props;
        if (userDetails.status && activeStatus !== userDetails.status.toLowerCase()) {
            this.setState({ activeStatus: userDetails.status.toLowerCase() });
        }
    }

    toggleUserStatus = (activityTypeId) => {
        let { toggleUserStatusAction } = this.props;
        let payload = { "action_type": activityTypeId };
        toggleUserStatusAction(payload);
    }

    render() {
        let { activityTypes, activeStatus } = this.state;
        let { activities } = this.props;
        return (
            <div className="activity-container">
                <PageHeader header={"Activity"} />
                <div className="activity-content">
                    <div className="activity-tabs-container">
                        {activityTypes.map((activityType, index) =>
                            <div key={`activity-type-${index}`} className="activity-tab">
                                <div
                                    className={activeStatus === activityType.key ? "activity-tab-content active" : "activity-tab-content"}
                                    onClick={() => activeStatus !== activityType.key && this.toggleUserStatus(activityType.id)}
                                >
                                    {activityType.label.toUpperCase()}
                                </div>
                                <div className="activity-desc">
                                    {activities[activityType.key] &&
                                        activities[activityType.key].map((activity, index) =>
                                            <div key={`activity-${activityType.key}-${index}`}>
                                                {getTime(activity.start_time)} - {getTime(activity.end_time)}
                                                {/* ({getTimeDifference(activity.start_time, activity.end_time)}) */}
                                            </div>)
                                    }
                                </div>
                            </div>)
                        }
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        userDetails: state.userDetails,
        activities: state.activities
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ getActivitiesAction, resetActivitiesAction, toggleUserStatusAction }, dispatch)
}

Activity.propTypes = {
    userDetails: PropTypes.object.isRequired,
    toggleUserStatusAction: PropTypes.func.isRequired,
    activities: PropTypes.object
}

export default connect(mapStateToProps, mapDispatchToProps)(Activity);
