import Cookies from 'universal-cookie';
import { getDateTime } from '../config/time';
import { generateRandomString } from './common';

const expires = 24 * 60 * 60;       //  days * hr * s

export const generateCookie = (data) => {
    const cookies = new Cookies();
    let authCookieStack = cookies.get('authCookieStack')
    if (authCookieStack) {
        console.log("Storing data in Cookie...");
        if (authCookieStack && Array.isArray(authCookieStack.login_stack)) {
            let stack = authCookieStack.login_stack || [];
            stack.push({
                "id": stack.length + 1,
                "username": data.username,
                "role": data.role,
                "timestamp": getDateTime("", "now")
            });
            authCookieStack = { ...authCookieStack, login_stack: stack }
        }
        cookies.set('authCookieStack', authCookieStack, { path: '/' });
    } else {
        console.log("Setting Cookie...");
        cookies.set('authCookieStack', {
            "uniqueID": generateRandomString(),
            "login_stack": [{
                "id": 1,
                "username": data.username,
                "role": data.role,
                "timestamp": getDateTime("", "now")
            }]
        }, {
            path: '/',
            // expires: Date().toUTCString()
            maxAge: expires             //
        });
    }
    console.log("Login Stack: ", cookies.get('authCookieStack')); // log
}

export const getCookieInString = () => {
    const cookies = new Cookies();
    return JSON.stringify(cookies.get('authCookieStack'))
}