import { GET_PRODUCTION_ORDERS, RESET_PRODUCTION_ORDERS } from './constants';

const initialState = {
    orders: null
};

export default function ProductionOrdersReducer(state = initialState, action) {
    switch (action.type) {
        case GET_PRODUCTION_ORDERS:
            return Object.assign({}, state, {
                orders: action.data
            });

        case RESET_PRODUCTION_ORDERS:
            return Object.assign({}, state, {
                orders: null
            });

        default: return state;
    }
}