import { SHOW_LOADER, HIDE_LOADER } from './constants';

export default function cetReducer(state = {}, action) {
    switch (action.type) {
        case SHOW_LOADER:
            return {
                showLoader: true
            }

        case HIDE_LOADER:
            return {
                showLoader: false
            }

        default:
            return state;
    }
}