export const getOrderStatus = () => {
    return orderStatus;
}

export const getOrderStatusById = (id) => {
    return orderStatus.filter(status => status.id === id).length > 0 ? orderStatus.filter(status => status.id === id)[0] : {};
}

export const getPossibleStatusCanBeUpdated = (currentStatusId) => {
    let statuses = orderStatus.filter(status => status.id === currentStatusId);
    let status = statuses.length > 0 ? statuses[0] : [];
    let possibleStatusToShow = status.possibleStatusToShow && status.possibleStatusToShow.length > 0 ? status.possibleStatusToShow : [];
    let newStatuses = possibleStatusToShow.length > 0 ? possibleStatusToShow.map(id => getOrderStatusById(id)) : []
    return newStatuses;
    // let statuses = orderStatus
    //     .filter(status => status.id === currentStatusId)[0].possibleStatusToShow
    //     .map(id => getOrderStatusById(id));
    // return statuses
}


export const getHoldPossibleStatusWithOrderConfirmed = (currentStatusId) => {
    console.log(currentStatusId)
    let statuses = orderStatus.filter(status => status.id === currentStatusId);
    let status = statuses.length > 0 ? statuses[0] : [];
    let possibleStatusWithConfirmedTime = status.possibleStatusWithConfirmedTime && status.possibleStatusWithConfirmedTime.length > 0 ? status.possibleStatusWithConfirmedTime : [];
    let newStatuses = possibleStatusWithConfirmedTime.length > 0 ? possibleStatusWithConfirmedTime.map(id => getOrderStatusById(id)) : []
    return newStatuses;
}

export const getStatusTabsByRole = (role) => {
    return orderStatus
        .filter(status => status.accessibleByRole.includes(role))
        .sort((prev, next) =>
            role === "RT" ?
                prev.orderByRT - next.orderByRT :
                role === "OC" ?
                    prev.orderByOC - next.orderByOC :
                    prev.orderBy - next.orderBy
        );
}