import { UPLOAD_SHEET_SUCCSESS, RESET_SHEET, RESET_SHEET_DATA } from "./constants";

const initialState = {
    result: null,
    onUploadSuccess: false
};

export default function LogisticReducer(state = initialState, action) {
    switch (action.type) {
        case UPLOAD_SHEET_SUCCSESS:
            return Object.assign({}, state, {
                result: action.data,
                onUploadSuccess: true
            });

        case RESET_SHEET:
            return Object.assign({}, state, {
                result: null,
                onUploadSuccess: false
            });


        case RESET_SHEET_DATA:
            return Object.assign({}, state, {
                onUploadSuccess: false
            });

        default: return state;
    }
}