import errorHandler from '../../errorHandler';
import { showLoader, hideLoader } from '../../components/Loader/action';
import { showPopup } from '../../components/Popup/action';
import { GET_ACTIVITIES_DATA, RESET_ACTIVITIES_DATA } from './constants';
import { getActivities } from '../../api';
import { toast } from 'react-toastify';

export function getActivitiesAction(memberId) {
    return (dispatch) => {
        dispatch(showLoader());
        getActivities(memberId)
            .then(response => {
                // dispatch(getActivitiesActionSuccess(response.data));
                dispatch(hideLoader());
                // memberId &&
                //     dispatch(showPopup({
                //         type: "activityPopup",
                //         header: "Activities",
                //         data: response.data
                //     }));
                if (response.status === 200) {
                    dispatch(getActivitiesActionSuccess(response.data));
                    memberId &&
                        dispatch(showPopup({
                            type: "activityPopup",
                            header: "Activities",
                            data: response.data
                        }));
                } else if (response.status === 204) {
                    toast.info("No Data Available")
                }
            })
            .catch(error => {
                errorHandler(error, dispatch);
            });
    }
}

export function getActivitiesActionSuccess(data) {
    return {
        type: GET_ACTIVITIES_DATA,
        data
    }
}

export function resetActivitiesAction() {
    return {
        type: RESET_ACTIVITIES_DATA,
    }
}
