import {
    GET_PLACE_NEWORDER_PRODUCTS_SUCCESS,
    PLACE_NEWORDER_SUCCESS,
    REPRESE_PLACE_NEW_ORDER,
    GET_STATE_CITY_SUCCESS,
    RESET_STATE_CITY,
    RESET_PLACE_NEWORDER_PRODUCTS_SUCCESS,
    VERIFY_GST_DATA_SUCCESS,
    RESET_GST_DATA,
    GETTING_PRODUCT_DETAILS,
    GET_PRODUCT_DETAILS_SUCCESS,
    GET_PRODUCT_DETAILS_ERROR,
    PLACE_NEWORDER_ERROR,
    RESET_PLACE_NEWORDER_SUCCESS_AND_ERROR,
    RESET_PRODUCT_DETAILS_DATA
} from "./constants";
import { showLoader, hideLoader } from "../../components/Loader/action";
import { toast } from "react-toastify";
import { placeorder, getPinDetails, getProductsPlaceNewOrder, productDetailsAPI, verifyGST } from "../../api";

import handleError from "../../errorHandler";

export function getProductsSuccess(data) {
    return {
        type: GET_PLACE_NEWORDER_PRODUCTS_SUCCESS,
        data
    }
}

export function resetProductsSuccess() {
    return {
        type: RESET_PLACE_NEWORDER_PRODUCTS_SUCCESS
    }
}

export function getProducts() {
    return (dispatch) => {
        dispatch(showLoader());
        getProductsPlaceNewOrder()
            .then(response => {
                dispatch(hideLoader());
                dispatch(getProductsSuccess(response.data));
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    };
}

export function getCitySuccess(data) {
    return {
        type: GET_STATE_CITY_SUCCESS,
        data
    }
}

export function resetStateCity() {
    return {
        type: RESET_STATE_CITY
    }
}

export function getStateCity(data) {
    return (dispatch) => {
        dispatch(showLoader());
        return getPinDetails(data)
            .then(response => {
                dispatch(hideLoader());
                // dispatch(resetStateCity());
                dispatch(getCitySuccess(response.data));
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    };
}

export function verifyGSTDetails(data) {
    return (dispatch) => {
        dispatch(showLoader());
        verifyGST(data)
            .then(response => {
                dispatch(hideLoader());
                dispatch(verifyGSTSuccess(response.data));
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    };
}

export function verifyGSTSuccess(data) {
    return {
        type: VERIFY_GST_DATA_SUCCESS,
        data
    }
}

export function resetGSTData() {
    return {
        type: RESET_GST_DATA
    }
}


export function resetProductDetails() {
    return {
        type: RESET_PRODUCT_DETAILS_DATA
    }
}

export function placeNewOrder(data) {
    return (dispatch) => {
        dispatch(showLoader());
        // dispatch(placeNewOrderSuccess({ "message": "Success" }));
        placeorder(data)
            .then(response => {
                dispatch(hideLoader());
                dispatch(placeNewOrderSuccess({ ...response.data, resetData: true }));
                // dispatch(refreshPlaceNewOrder(true));
                dispatch(getProducts());
                toast.success(response.data.message);
            })
            .catch(error => {
                dispatch(placeNewOrderError(error.response.data));
                handleError(error, dispatch);
            });
    };
}

export function placeNewOrderSuccess(data) {
    return {
        type: PLACE_NEWORDER_SUCCESS,
        data
    }
}

export function placeNewOrderError(error) {
    return {
        type: PLACE_NEWORDER_ERROR,
        error
    }
}

export function resetPlaceNewOrderSuccessAndError(error) {
    return {
        type: RESET_PLACE_NEWORDER_SUCCESS_AND_ERROR,
        error
    }
}

export function refreshPlaceNewOrder(trueOrFalse) {
    return {
        type: REPRESE_PLACE_NEW_ORDER,
        data: trueOrFalse
    }
}

export function getProductDetailsAction(data) {
    return (dispatch) => {
        dispatch(gettingProductData(data));
        productDetailsAPI(data)
            .then(response => {
                dispatch(getProductDetailsSuccess(response.data));
            })
            .catch(error => {
                dispatch(getProductDetailsError(error));
                handleError(error, dispatch);
            });
    };
}

export function gettingProductData(data) {
    return {
        type: GETTING_PRODUCT_DETAILS,
        data
    }
}

export function getProductDetailsSuccess(data) {
    return {
        type: GET_PRODUCT_DETAILS_SUCCESS,
        data
    }
}

export function getProductDetailsError(error) {
    return {
        type: GET_PRODUCT_DETAILS_ERROR,
        error
    }
}