export const decimalRegex = /^\d+(\.\d{0,2})?$/;
export const dimensionRegex = /^\d{0,2}(\.\d{0,1})?$/;
export const numberRegex = /^[0-9]*$/;
export const charsRegex = /^[a-zA-Z\s]*$/;
export const nameRegex = /^[a-zA-Z\s.]*$/;
export const addressRegex = /^[a-zA-Z0-9_\-:,.#@' '\n]*$/;
export const emailRegex = /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)+$/;
export const commentsRegex = /^[a-zA-Z0-9_\-+*/\\&:,.#@' ']*$/;
export const numberSpiltRegex = /[+-]?\d+(?:\.\d+)?/g;
export const gstRegex = /^\d{2}[A-Z]{5}\d{4}[A-Z]{1}[A-Z\d]{1}[Z]{1}[A-Z\d]{1}/;

