export const shipmentBlockedReasons = [{
    id: 1,
    label: "Dimensions Change",
    is_active: 1
}, {
    id: 2,
    label: "Address Change",
    is_active: 1
}, {
    id: 3,
    label: "Customer Request",
    is_active: 1
}, {
    id: 4,
    label: "Order quantity change",
    is_active: 1
}, {
    id: 5,
    label: "Price changes",
    is_active: 1
}, {
    id: 6,
    label: "Cart Split",
    is_active: 1
}, {
    id: 7,
    label: "BLR Local Invoice",
    is_active: 1
}];

export const getShipmentBlockedReason = (id) => {
    let reason = shipmentBlockedReasons.filter((reason) => Number(reason.id) === Number(id));
    return reason.length > 0 ? reason[0].label : "";
}