import axios from 'axios';
import { getCookieInString } from '../utilities/cookies';

let authToken = null;
let apiURLOriginal = process.env.NODE_ENV === 'development' ? process.env.REACT_APP_API_URL_DEV : process.env.REACT_APP_API_URL_PROD;

export { apiURLOriginal };

let apiURL = apiURLOriginal + 'api/';

if (localStorage.getItem('authToken')) {
    authToken = localStorage.getItem('authToken');
}

var AxiosAPI = authToken ? axios.create({
    baseURL: apiURL,
    headers: {
        common: {
            'Authorization': 'Bearer ' + authToken,
            'Api-Token': process.env.REACT_APP_API_TOKEN,
            'Api-Secret-Key': process.env.REACT_APP_API_SECRET_KEY,
            'My-Cookie': getCookieInString(),               //  gets JSON.stringify of cookie content
            // 'Access-Control-Allow-Origin': '*'
        }
    },
    timeout: 180 * 1000     //  seconds
}) : axios.create({
    baseURL: apiURL,
    headers: {
        common: {
            'Api-Token': process.env.REACT_APP_API_TOKEN,
            'Api-Secret-Key': process.env.REACT_APP_API_SECRET_KEY,
            'My-Cookie': getCookieInString(),               //  gets JSON.stringify of cookie content
            // 'Access-Control-Allow-Origin': '*'
        }
    },
    timeout: 180 * 1000     //  seconds
});

const setAxiosAPIWithAuthToken = () => {
    let authToken = localStorage.getItem('authToken');
    if (AxiosAPI && authToken) {
        AxiosAPI.defaults.headers.Authorization = 'Bearer ' + authToken;
    }
    return AxiosAPI;
}

export { AxiosAPI, setAxiosAPIWithAuthToken };