import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { Menu, MenuItem } from '@material-ui/core';
import { Link } from 'react-router-dom';
import './style.scss';

const CustomMenuItem = (props) =>
    <Link to={props.link}>
        <MenuItem
            onClick={props.handleClose}
            style={{ fontSize: '12px', color: '#3d2277' }}
        >
            {props.label}
        </MenuItem>
    </Link>

class HeaderMenu extends Component {
    state = {
        element: null,
        isMenuOpen: false
    }

    handleMenuOpen = (event) => this.setState({ element: event.currentTarget, isMenuOpen: true });

    handleMenuClose = () => this.setState({ isMenuOpen: false });

    getMenuItems = () => {
        let { element, isMenuOpen } = this.state;
        let { items } = this.props;
        return (
            <Menu
                style={{ top: '40px' }}
                anchorEl={element}
                anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                transformOrigin={{ vertical: 'top', horizontal: 'right' }}
                open={isMenuOpen}
                onClose={this.handleMenuClose}
            >
                {items &&
                    Array.isArray(items) &&
                    items.length > 0 &&
                    items.map((item, index) => {
                        return (
                            <CustomMenuItem
                                {...item}
                                handleClose={this.handleMenuClose}
                            />
                        );
                    })
                }
            </Menu>
        );
    }

    render() {
        let { text } = this.props;
        return (
            <div className="custom-menu-contanier">
                <NavLink
                    className="custom-menu-item"
                    activeClassName="custom-menu-item-active"
                    onClick={this.handleMenuOpen}
                >
                    {text}
                </NavLink>
                {this.getMenuItems()}
            </div>
        );
    }
}

export default HeaderMenu;
