import { GET_ALL_CET_MEMBERS, EDIT_CET_MEMBER_SUCCESS, RESET_ALL_CETS_DATA, GET_ALL_POD_LEADERS, UPDATE_MEMBERS_POD_LEADERS } from "./constants";

const initialState = {
    cetsMembers: null,
    podLeaders: [],
    refreshCetMembers: false
};

export default function listCetReducer(state = initialState, action) {
    switch (action.type) {
        case GET_ALL_CET_MEMBERS:
            return Object.assign({}, state, {
                cetsMembers: action.data,
                refreshCetMembers: false
            });

        case GET_ALL_POD_LEADERS:
            return Object.assign({}, state, {
                podLeaders: action.data,
                refreshCetMembers: false
            });

        case EDIT_CET_MEMBER_SUCCESS:
            return Object.assign({}, state, {
                refreshCetMembers: true
            });

        case UPDATE_MEMBERS_POD_LEADERS:
            return Object.assign({}, state, {
                refreshCetMembers: true
            });

        case RESET_ALL_CETS_DATA:
            return Object.assign({}, state, { ...initialState });

        default: return state;
    }
}