export const getReturnDocketStatuses = () => {
    return returnDocketStatuses;
}

export const getReturnDocketStatusById = (id) => {
    let status = returnDocketStatuses.filter(status => status.id === id);
    return status.length > 0 ? status[0] : {};
}

export const getReturnPickupStatuses = () => {
    return returnPickupStatuses;
}

const refundStatus = [{
    id: -1,
    name: "Not Applicable"
}, {
    id: 1,
    name: "Initiated"
}, {
    id: 2,
    name: "Completed"
}];

export const getReturnStatus = () => {
    return returnReplacement;
}

export const getReturnStatusById = (id) => {
    let reason = returnReplacement.filter(reason => reason.id === id);
    return reason.length > 0 ? reason[0] : {};
}

export const getReturnReplacementReasons = () => {
    return returnReplacementReasons;
}

export const getReasonById = (id) => {
    let reason = returnReplacementReasons.filter(reason => reason.id === id);
    return reason.length > 0 ? reason[0] : {};
}

export const getRefundStatus = () => {
    return refundStatus;
}

export const getRefundStatusById = (id) => {
    let refund = refundStatus.filter(refund => refund.id === id);
    return refund.length > 0 ? refund[0] : {};
}
