import { UPLOAD_SHEET_SUCCESS, RESET_SHEET_DETAILS, RESET_COMPOENENT_DETAILS } from "./constants";

const initialState = {
    successData: null,
    reset: false
};

export default function importSheetFinanceReducer(state = initialState, action) {
    switch (action.type) {
        case UPLOAD_SHEET_SUCCESS:
            return Object.assign({}, state, {
                successData: action.data,
                reset: false
            });

        case RESET_SHEET_DETAILS:
            return Object.assign({}, state, {
                successData: null,
                reset: true
            });

        case RESET_COMPOENENT_DETAILS:
            return Object.assign({}, state, { ...initialState });

        default: return state;
    }
}