import {
    CALL_CUSTOMER_SUCCESS,
    GET_COMMENTS_PER_ORDER_SUCCESS,
    ORDER_SEARCH_SUCCESS,
    UPDATE_PAYMENT_TRACK_ID_SUCCESS,
    GET_ORDERS_SUCCESS,
    RESET_ORDERS_DATA,
    UPDATE_ORDER_STATUS_SUCCESS,
    GET_CUSTOMER_DETAILS,
    RESET_CUSTOMER_DETAILS,
    RETURN_REPLACE_SUCCESS,
    RESET_REFRESH_ORDERS,
    CANCEL_RETURN_REPLACEMENT,
    CANCEL_ORDER_OR_CART_SUCCESS,
    CANCEL_CANCELLATION_REQUEST,
    REFRESH_ORDERS,
    SPLIT_ORDER_SUCCESS
} from './constants';
import { UPDATE_CURRENT_STATUS_SUCCESS } from '../OcModule/constants';

const initialState = {
    ordersData: {},
    orderIdSearch: false,
    refreshOrders: false,
    lastCallConnected: {},
    customerDetails: {},
    lastCurrentStatusUpdated: {}
}

export default function dashboardReducer(state = initialState, action) {
    switch (action.type) {
        case GET_ORDERS_SUCCESS:
            return Object.assign({}, state, {
                ordersData: action.data,
                orderIdSearch: false,
                refreshOrders: false
            });

        case ORDER_SEARCH_SUCCESS:
            return Object.assign({}, state, {
                ordersData: action.data,
                orderIdSearch: true,
                refreshOrders: false
            });

        case RESET_ORDERS_DATA:
            return Object.assign({}, state, {
                ordersData: {},
                refreshOrders: false,
                orderIdSearch: false
            });

        case UPDATE_ORDER_STATUS_SUCCESS:
            return Object.assign({}, state, {
                lastOrderStatusUpdated: action.data,
                refreshOrders: true
            });

        case CALL_CUSTOMER_SUCCESS:
            return Object.assign({}, state, {
                lastCallConnected: action.data
            });

        case UPDATE_PAYMENT_TRACK_ID_SUCCESS:
            return Object.assign({}, state, {
                lastPaymentUpdated: action.data,
                refreshOrders: true
            });

        case GET_COMMENTS_PER_ORDER_SUCCESS:
            return Object.assign({}, state, {
                commentsPerOrder: action.data
            });

        case GET_CUSTOMER_DETAILS:
            return Object.assign({}, state, {
                customerDetails: action.data
            });

        case RESET_CUSTOMER_DETAILS:
            return Object.assign({}, state, {
                customerDetails: {}
            });

        // from OC

        case UPDATE_CURRENT_STATUS_SUCCESS:
            return Object.assign({}, state, {
                lastCurrentStatusUpdated: action.data,
                refreshOrders: true
            });

        //  for Universal Order Search
        case RETURN_REPLACE_SUCCESS:
            return Object.assign({}, state, {
                refreshOrders: true
            });

        case REFRESH_ORDERS:
            return Object.assign({}, state, {
                refreshOrders: true
            });

        case RESET_REFRESH_ORDERS:
            return Object.assign({}, state, {
                refreshOrders: false
            });

        case CANCEL_RETURN_REPLACEMENT:
            return Object.assign({}, state, {
                refreshOrders: true
            });

        case CANCEL_ORDER_OR_CART_SUCCESS:
        case SPLIT_ORDER_SUCCESS:
        case CANCEL_CANCELLATION_REQUEST:
            return Object.assign({}, state, {
                refreshOrders: true
            });

        default:
            return state;
    }
}
