import { SHOW_POPUP, HIDE_POPUP } from './constants';

export default function popup(state = {}, action) {
    switch (action.type) {
        case SHOW_POPUP:
            return Object.assign({}, state, {
                ...action.data
            });

        case HIDE_POPUP:
            return Object.assign({}, {}, {});

        default:
            return state;
    }
}