import { GET_SALES_MEMBERS_SUCCESS, RESET_SALES_MEMBERS_DATA, EDIT_SALES_MEMBERS_SUCCESS } from "./constants";

const initialState = {
    members: [],
    // refreshMembers: false
};

export default function salesReducer(state = initialState, action) {
    switch (action.type) {
        case GET_SALES_MEMBERS_SUCCESS:
            return Object.assign({}, state, {
                members: action.data,
                // refreshMembers: false
            });

        case RESET_SALES_MEMBERS_DATA:
            return Object.assign({}, state, {
                members: [],
                // refreshMembers: false
            });

        case EDIT_SALES_MEMBERS_SUCCESS:
            console.log("Editing in redux", action.data);
            let updatedMembers = state.members.map((member) => member.login_id === action.data.login_id ? { ...member, ...action.data } : member)
            return Object.assign({}, state, {
                members: updatedMembers,
                // refreshMembers: true // uncomment if you want to pull from API and comment above
            });

        default: return state;
    }
}