import { GET_COUPONS_DETAILS, GET_COUPON_ORDERS_DETAILS, RESET_COUPON_DATA } from './constants';

const initialState = {
    couponsDetails: null,
    couponsOrders: null
};

export default function CouponsDetailsReducer(state = initialState, action) {
    switch (action.type) {
        case GET_COUPONS_DETAILS:
            return Object.assign({}, state, {
                couponsDetails: action.data
            });

        case GET_COUPON_ORDERS_DETAILS:
            return Object.assign({}, state, {
                couponsOrders: action.data
            });

        case RESET_COUPON_DATA:
            return Object.assign({}, state, {
                couponsOrders: null,
                couponsDetails: null
            });
        default: return state;
    }

}