import { GET_REVIEW_ORDERS_DATA, GET_UPDATED_REVIEW_ORDERS_DATA,RESET_REVIEW_ORDERS_DATA } from './constants';

const initialState = {
    reviewOrders: null,
    refreshData: false
};

export default function ProductReviewReducer(state = initialState, action) {
    switch (action.type) {
        case GET_REVIEW_ORDERS_DATA:
            return Object.assign({}, state, {
                reviewOrders: action.data,
                refreshData: false
            });

        case GET_UPDATED_REVIEW_ORDERS_DATA:
            return Object.assign({}, state, {
                refreshData: true
            });

        case RESET_REVIEW_ORDERS_DATA:
            return Object.assign({}, state, {
                reviewOrders: null,
                refreshData: false
            });
        default: return state;
    }
}