import React from 'react';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';

import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';

import Header from '../components/Header';
import Footer from '../components/Footer';
import Loader from '../components/Loader';

import Homepage from './Homepage';
import Login from './Login/loadable';
import ImportSheets from './ImportSheets/loadable';
import ImportLogisticSheets from './ImportLogisticSheet/loadable';
import StockManagement from './StockManagement/loadable';
import ProductionOrders from './ProductionOrders/loadable';
import ChangePassword from './ChangePassword/loadable';
import OrderSearch from './OrderSearch/loadable';
import TotalCalls from './TotalCalls/loadable';
import PlaceNewOrder from './PlaceNewOrder/loadable';
import NotFoundPage from './NotFoundPage/loadable';
import CartEdit from './CartEdit/loadable';
// import { getUrlParameter } from '../utilities/getUrlParamsValue';
import PageHeader from '../components/PageHeader';
import BreakComponent from '../components/BreakComponent';
import Activity from './Activity';
import CreateMember from './CreateMember/loadable';
import ResetPassword from './ResetPassword/loadable';
import ListAllCets from './ListAllCets/loadable';
import SalesModule from './SalesModule/loadable';
import RTModule from './RTModule/loadable';
import OcModule from './OcModule/loadable';
import ReturnsModule from './ReturnsModule/loadable';
import FinanceModule from './FinanceModule/loadable';
import CetActivityList from './CetActivityList/loadable';
import UniversalCallSearch from './UniversalCallSearch/loadable';
import Reports from './Reports/loadable';
import CodToOnline from './CodToOnline/loadable';
import Dialer from '../components/Dialer';
import MissingItems from './MissingItems/loadable';
import ProcessInvoiceDocket from './ProcessInvoiceDocket/loadable';
import ListAllCoupons from './ListAllCoupons/loadable';
import CreateCoupon from './CreateCoupon/loadable';
import CouponOrderDetails from './CouponOrderDetails/loadable';
import ProductReview from './ProductReviewAndQA/loadable';
import ProductReviewEdit from './ProductReviewAndQAEdit/loadable';
import UploadCODCollectionSheet from './UploadCODCollectionSheet/loadable';

function PrivateRoute({ component: Component, ...rest }) {
    return (
        <Route
            {...rest}
            render={props =>
                localStorage.getItem('authToken') ? (
                    <Component {...props} />
                ) : (
                        <Redirect
                            to={{
                                pathname: "/login",
                                redirect: props.location
                            }}
                        />
                    )
            }
        />
    );
}

class App extends React.Component {
    render() {
        let { userDetails } = this.props;
        return (
            <div className="app">
                <BrowserRouter>
                    <Header />
                    {userDetails.status && userDetails.status.toLowerCase() === "break" &&
                        <div className="content-container">
                            <PageHeader header={"You are on Break!"} />
                            <BreakComponent userDetails={userDetails} />
                        </div>
                    }
                    {(Object.keys(userDetails).length === 0 || (userDetails.status && userDetails.status.toLowerCase() === "working")) &&
                        <div className="content-container">
                            <Switch>
                                <Route exact path="/" component={Homepage} />
                                <Route exact path="/login" component={Login} />

                                <PrivateRoute exact path="/relationship-team" component={(props) => {
                                    return <Redirect to={"/relationship-team/wakefit/instructions-due"} />
                                }} />

                                <PrivateRoute exact path="/order-confirmation-team" component={(props) => {
                                    return <Redirect to={"/order-confirmation-team/wakefit/new"} />
                                }} />

                                <PrivateRoute exact path="/returns-replacement" component={(props) => {
                                    return <Redirect to={"/returns-replacement/wakefit/requested"} />
                                }} />

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("TEAML") || userDetails.role.includes("RT")) &&
                                    <PrivateRoute exact path="/relationship-team/:platform/:slug" component={(props) => {
                                        return <RTModule {...props} platform={props.match.params.platform} slug={props.match.params.slug} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("TEAML") || userDetails.role.includes("RT")) &&
                                    <PrivateRoute exact path="/relationship-team/:cet_uid/:platform/:slug" component={(props) => {
                                        return <RTModule {...props} cetId={props.match.params.cet_uid} platform={props.match.params.platform} slug={props.match.params.slug} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("TEAML") || userDetails.role.includes("OC")) &&
                                    <PrivateRoute exact path="/order-confirmation-team/:platform/:slug" component={(props) => {
                                        return <OcModule {...props} platform={props.match.params.platform} slug={props.match.params.slug} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("TEAML") || userDetails.role.includes("OC")) &&
                                    <PrivateRoute exact path="/order-confirmation-team/:cet_uid/:platform/:slug" component={(props) => {
                                        return <OcModule {...props} cetId={props.match.params.cet_uid} platform={props.match.params.platform} slug={props.match.params.slug} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (!userDetails.role.includes("FINANCE")) &&
                                    <PrivateRoute
                                        exact
                                        path="/returns-replacement/:platform"
                                        component={(props) => {
                                            return <ReturnsModule
                                                {...props}
                                                userRole={userDetails.role.includes("OPS") ? "OPS" : "C"}
                                                platform={props.match.params.platform}
                                            //   slug={props.match.params.slug}
                                            />
                                        }} />
                                }

                                {Array.isArray(userDetails.role) && (!userDetails.role.includes("FINANCE")) &&
                                    <PrivateRoute
                                        exact
                                        path="/returns-replacement/:platform/:slug"
                                        component={(props) => {
                                            return <ReturnsModule
                                                {...props}
                                                userRole={userDetails.role.includes("OPS") ? "OPS" : "C"}
                                                cetId={props.match.params.cet_uid}
                                                platform={props.match.params.platform}
                                                slug={props.match.params.slug}
                                            />
                                        }}
                                    />
                                }

                                {Array.isArray(userDetails.role) && (!userDetails.role.includes("FINANCE")) &&
                                    <PrivateRoute
                                        exact
                                        path="/returns-replacement/:cet_uid/:platform/:slug"
                                        component={(props) => {
                                            return <ReturnsModule
                                                {...props}
                                                userRole={userDetails.role.includes("OPS") ? "OPS" : "C"}
                                                cetId={props.match.params.cet_uid}
                                                platform={props.match.params.platform}
                                                slug={props.match.params.slug}
                                            />
                                        }} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("SALES") &&
                                    <PrivateRoute exact path="/sales-team" component={SalesModule} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("TEAML") || userDetails.role.includes("SALES")) &&
                                    <PrivateRoute exact path="/create-member" component={CreateMember} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("TEAML") || userDetails.role.includes("SALES")) &&
                                    <PrivateRoute exact path="/reset-password/:login_id" component={({ match, location }) => {
                                        return <ResetPassword login_id={match.params.login_id} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("OC") || userDetails.role.includes("RT") || userDetails.role.includes("C") || userDetails.role.includes("SALES")) &&
                                    <PrivateRoute exact path="/activity" component={Activity} />
                                }

                                {
                                    // Array.isArray(userDetails.role) && (userDetails.role.includes("TEAML") || userDetails.role.includes("OC") || userDetails.role.includes("RT") || userDetails.role.includes("C") || userDetails.role.includes("SALES")) &&
                                    <PrivateRoute exact path="/total-calls" component={TotalCalls} />
                                }

                                <PrivateRoute exact path="/cart-edit/:affiliateId/:cartId/" component={({ match, location }) => {
                                    return <CartEdit cartId={match.params.cartId} affiliateId={match.params.affiliateId}
                                    // role={getUrlParameter("role")} 
                                    />
                                }} />

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("TEAML") || userDetails.role.includes("QA")) &&
                                    <PrivateRoute exact path="/universal-call-search" component={UniversalCallSearch} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("TEAML") &&
                                    <PrivateRoute exact path="/reports" component={Reports} />
                                }

                                {([9, 24, 89].includes(userDetails.login_id) && Array.isArray(userDetails.role) && userDetails.role.includes("TEAML")) &&
                                    <PrivateRoute exact path="/import-logistics-sheet" component={ImportLogisticSheets} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("OPS") &&
                                    <PrivateRoute exact path="/import-orders" component={ImportSheets} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("OPS") &&
                                    <PrivateRoute exact path="/import-logistics-sheet" component={ImportLogisticSheets} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("OPS") &&
                                    <PrivateRoute exact path="/stock-management" component={StockManagement} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("OPS") &&
                                    <PrivateRoute exact path="/stuck-orders" component={ProductionOrders} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("OPS") &&
                                    <PrivateRoute exact path="/cod-to-online" component={CodToOnline} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("OPS") &&
                                    <PrivateRoute exact path="/missing-items" component={MissingItems} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("OPS") &&
                                    <PrivateRoute exact path="/invoice-docket-process" component={ProcessInvoiceDocket} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("TEAML") &&
                                    <PrivateRoute exact path="/list-cets" component={ListAllCets} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("FINANCE") &&
                                    <PrivateRoute exact path="/refund-list" component={FinanceModule} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("FINANCE") &&
                                    <PrivateRoute exact path="/refund-list/:refundCategory/" component={(props) => {
                                        return <FinanceModule {...props} refundCategory={props.match.params.refundCategory} platform={props.match.params.platform} status={props.match.params.status} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("FINANCE") &&
                                    <PrivateRoute exact path="/refund-list/:refundCategory/:platform/" component={(props) => {
                                        return <FinanceModule {...props} refundCategory={props.match.params.refundCategory} platform={props.match.params.platform} status={props.match.params.status} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("FINANCE") &&
                                    <PrivateRoute exact path="/refund-list/:refundCategory/:platform/:status" component={(props) => {
                                        return <FinanceModule {...props} refundCategory={props.match.params.refundCategory} platform={props.match.params.platform} status={props.match.params.status} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("FINANCE") &&
                                    <PrivateRoute exact path="/upload-sheet" component={UploadCODCollectionSheet} />
                                }

                                {Array.isArray(userDetails.role) && userDetails.role.includes("TEAML") &&
                                    <PrivateRoute exact path="/activity-status-cet-sales" component={CetActivityList} />
                                }

                                {Array.isArray(userDetails.role) && !userDetails.role.includes("FINANCE") &&
                                    <PrivateRoute exact path="/place-new-order" component={PlaceNewOrder} />
                                }

                                {Array.isArray(userDetails.role) && !userDetails.role.includes("FINANCE") &&
                                    <PrivateRoute exact path="/place-new-order/:platform/:orderId/" component={({ match, location }) => {
                                        return <PlaceNewOrder orderId={match.params.orderId} platform={match.params.platform} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/coupons" component={ListAllCoupons} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/coupons/add" component={CreateCoupon} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/coupons/edit/:couponId" component={({ match, location }) => {
                                        return <CreateCoupon couponId={match.params.couponId} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/coupons/:coupon_uid" component={({ match, location }) => {
                                        return <CouponOrderDetails couponUid={match.params.coupon_uid} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/list-of-review-orders" component={(props) => {
                                        return <Redirect to={"/list-of-review-orders/list"} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/list-of-review-orders/:platform" component={(props) => {
                                        return <ProductReview {...props} platform={props.match.params.platform} />
                                    }} />
                                }
                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/user-review-edit/:id" component={({ match, props, location }) => {
                                        return <ProductReviewEdit {...props} ReveiwAndQAId={match.params.id} requestType="review" />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/list-of-qa-orders" component={(props) => {
                                        return <Redirect to={"/list-of-qa-orders/list"} />
                                    }} />
                                }

                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/list-of-qa-orders/:platform" component={(props) => {
                                        return <ProductReview {...props} platform={props.match.params.platform} />
                                    }} />
                                }
                                {Array.isArray(userDetails.role) && (userDetails.role.includes("ADMIN") || userDetails.role.includes("COUPON MANAGER")) &&
                                    <PrivateRoute exact path="/user-qa-edit/:id" component={({ match, props, location }) => {
                                        return <ProductReviewEdit {...props} ReveiwAndQAId={match.params.id} requestType="qa" />
                                    }} />
                                }

                                <PrivateRoute exact path="/order-search" component={OrderSearch} />
                                <PrivateRoute exact path="/change-password" component={ChangePassword} />

                                <Route component={NotFoundPage} />
                            </Switch>
                        </div>}
                    <Footer />
                    <Loader />
                </BrowserRouter>
                <ToastContainer
                    autoClose={5000}
                    newestOnTop
                />
                {localStorage.getItem("authToken") &&
                    userDetails &&
                    userDetails.contact &&
                    <Dialer />
                }
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        userDetails: state.userDetails
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({}, dispatch)
}

App.defaultProps = {
    userDetails: {}
}

App.propTypes = {
    userDetails: PropTypes.object.isRequired,
}

export default connect(mapStateToProps, mapDispatchToProps)(App);