import React, { Component } from 'react';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import { getAllCallDetails } from '../../../containers/TotalCalls/action';
import TotalCalls from '../../../containers/TotalCalls/components/TableTotalCalls';
import './style.scss';

class CallRecordings extends Component {


  getCallDetails = () => {
    //  Get The Contact Number from Token (for Backend)
    this.props.getAllCallDetails('', '', '', '', "+91" + this.props.data.props, "all");
    //this.props.getAllCallDetails('', '', '', '', "+917008716375")
  }

  componentDidMount() {
    this.setState({ callType: '' }, () => this.getCallDetails());
  }
  render() {
    console.log(this.props);
    return (
      <div className="call-details">
        <TotalCalls callDetails={this.props.callDetails} />
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    callDetails: state.callDetails.calls,
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ getAllCallDetails }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(CallRecordings);