import React, { Component } from 'react';
import './style.scss';
import { getTime } from '../../../config/time';

export default class CetActivities extends Component {
    render() {
        let { data } = this.props;
        let activityTypes = ["working", "break", "stop"];
        return (
            <div className="activity-container-popup">
                {data && (activityTypes.map((type, index) => <div className="activity-type" key={`activity-${type}-${index}`}>
                    <div className="activity-label">{type}</div>
                    <div className="activities">
                        {data[type].map((activity, index) =>
                            <div className="activity-time" key={`wokring-${index}`}>{getTime(activity.start_time)} - {getTime(activity.end_time)}</div>)}
                    </div>
                </div>))}
            </div>
        )
    }
}
