import { CREATE_MISSING_ITEM_SUCCSESS, RESET_MISSING_ITEM_DATA } from "./constants";

const initialState = {
    result: null,
    onCreateItemSuccess: false
};

export default function MissindgItemsReducer(state = initialState, action) {
    switch (action.type) {
        case CREATE_MISSING_ITEM_SUCCSESS:
            return Object.assign({}, state, {
                result: action.data,
                onCreateItemSuccess: true
            });

        case RESET_MISSING_ITEM_DATA:
            return Object.assign({}, state, {
                result: null,
                onCreateItemSuccess: false
            });

        default: return state;
    }
}