import React, { Component } from 'react';
import './style.scss';
import Button from '../../Button';
import { getReturnReplacementReasons } from '../../../config/returnReplacement';
// import { commentsRegex } from '../../../config/regex';

class ReturnReplacement extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {
                return_reason_id: 0,
                return_comments: "",
                attachments: null,
            },
            errors: {
                return_reason_id: null,
                return_comments: null,
                attachments: null,
            },
            enableSubmit: false,
            reasons: []
        }
    }

    componentWillMount() {
        this.setState({ reasons: getReturnReplacementReasons() });
    }

    handleChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;

        let { data } = this.state;
        let { errors } = this.state;

        if (fieldName === "attachments") {
            let images = [];
            let { files } = event.target;
            let allowedImageSize = 5 * 1024 * 1024; // 5MB Image size
            if (Object.keys(files).length <= 5) {
                // Allows max 5 Image upload
                let uploadedImageCount = 0; //  To check all are in size of allowedImageSize
                for (let key in files) {
                    if (key !== "length" && key !== "item") {
                        //  Allow the images only
                        // console.log(key, "=>", files[key]);

                        if (files[key].size <= allowedImageSize) {
                            let image = files[key];
                            let reader = new FileReader();
                            reader.readAsDataURL(image);
                            reader.onloadend = () => {
                                images.push(reader.result.split(",")[1]);
                                // images.push(reader.result);
                            }
                            uploadedImageCount++;
                        } else {
                            //  Break the loop of one of the Image exceed the allowedImageSize
                            data["attachments"] = [];
                            errors["attachments"] = "Image size should be less than 5 MB!";
                            break;
                        }
                    }
                }
                if (uploadedImageCount === Object.keys(files).length) {
                    //  Store Uploaded error free Images
                    data["attachments"] = images;
                    errors["attachments"] = "";
                }
            } else if (Object.keys(files).length > 5) {
                //  Through error If more imgaes uploaded
                data["attachments"] = images;
                errors["attachments"] = "Only 5 Images are allowed!";
            }
        } else {
            switch (fieldName) {
                case "return_comments":
                    //  Validation for Comments
                    if (!fieldValue) {
                        data[fieldName] = fieldValue;
                        errors[fieldName] = "Comments is Required!";
                    } else if (fieldValue.length >= 5) {
                        data[fieldName] = fieldValue;
                        errors[fieldName] = "";
                    } else if (fieldValue.length < 5) {
                        data[fieldName] = fieldValue;
                        errors[fieldName] = "Comments length should be more than 5";
                    }
                    break;

                default:
                    //  Store Rest data as it is
                    data[fieldName] = fieldValue;
                    break;
            }
        }
        this.setState({ data, errors }, () => this.validate());
    }

    validate = () => {
        let { data, errors } = this.state;
        let hasErrors = false, validForm = false;
        let requiredFields = ["return_reason_id", "return_comments", "attachments"];
        validForm = requiredFields.every(field => data[field] !== "");
        hasErrors = Object.keys(errors).length !== 0 && requiredFields.some(field => errors[field] && errors[field] !== "");
        this.setState({
            enableSubmit:
                !hasErrors &&
                validForm
        });
    }

    onSubmit = (event) => {
        event.preventDefault();
        let { data } = this.state;
        let { rowInfo } = this.props.data.popup;
        data["type"] = this.props.data.popup.type;
        data["affiliate_id"] = rowInfo.affiliate_id;
        data["order_id"] = rowInfo.order_random_id;
        data["cart_id"] = rowInfo.id;
        // data["role"] = "OC"

        //  API Call Through Action
        this.props.data.popup.onSubmit(data);
    }

    render() {
        let { data, errors, reasons } = this.state;
        return (
            <div className="return-replacement-container">
                <div className="field-container">
                    <div className="label">Reason</div>
                    <div>
                        <select className="custom-dropdown" name="return_reason_id" value={data.return_reason_id} onChange={this.handleChange}>
                            <option key={`reason-0`} value={0}>Select Reason</option>
                            {reasons.map((reason, index) => <option key={`reason-${index + 1}`} value={reason.id}>{reason.name}</option>)}
                        </select>
                    </div>
                </div>
                <div className="field-container">
                    <div className="label">Comments</div>
                    <div>
                        <textarea
                            name="return_comments"
                            className="input-field"
                            value={data.return_comments}
                            rows={4}
                            onChange={this.handleChange}
                        />
                        <div className="error-text">{errors.return_comments}</div>
                    </div>
                </div>
                <div className="field-container">
                    <div className="label">Attachment</div>
                    <div>
                        <input
                            name="attachments"
                            className="attachment"
                            type="file"
                            multiple={true}
                            // value={data.attachments}
                            accept="image/png, image/jpeg"
                            onChange={this.handleChange}
                            encType="multipart/form-data"
                        />
                        <div className="error-text">{errors.attachments}</div>
                    </div>
                </div>
                <div className="rnr-footer">
                    <Button
                        disabled={!this.state.enableSubmit}
                        label={`Request ${this.props.data.popup.type === "return" ? "Return" : "Replacement"}`}
                        onClick={this.onSubmit}
                    />
                </div>
            </div>
        );
    }
}

export default ReturnReplacement;
