import { GET_ORDER_SEARCH_SUCCESS, RESET_ORDER_SEARCH, RESET_ORDER_SEARCH_COMPONENT, GET_ALL_Q_C_DATA, UPDATE_OPERATION_DATA_SUCCESS } from './constants';

const intialState = {
    searchResults: null,
    QueryandConcernData: null
}

export default function orderSearchReducer(state = intialState, action) {
    switch (action.type) {
        case GET_ORDER_SEARCH_SUCCESS:
            return Object.assign({}, state, {
                searchResults: action.data
            });

        case RESET_ORDER_SEARCH:
        case UPDATE_OPERATION_DATA_SUCCESS:
            return Object.assign({}, state, {
                searchResults: null
            });

        case RESET_ORDER_SEARCH_COMPONENT:
            return Object.assign({}, state, {
                searchResults: null,
                QueryandConcernData: null
            });

        case GET_ALL_Q_C_DATA:
            return Object.assign({}, state, {
                // searchResults: null,
                QueryandConcernData: action.data
            });

        default:
            return state;
    }
}