import React, { Component } from 'react';
import './style.scss';

import { getDateTime } from '../../../config/time';
import { convertCurrency } from '../../../utilities/commaSeperation';
import { getPaymentResourceById } from '../../../config/payments';

class ModificationHistory extends Component {
    render() {
        return (
            <div className="modify-container">
                {Array.isArray(this.props.modificationHistory) && this.props.modificationHistory.map((modified, index) => {
                    // let orderStatusText = getOrderStatusById(modified.order_status).label;
                    return <div className="modify-content" key={`modified-item-${index}`}>
                        <div className="modify-header">
                            <div className="status">{modified.addon_payment_extra_or_refund}</div>
                            <div>{getDateTime(modified.addon_created_datetime)}</div>
                        </div>
                        <div className='cart-details1'>
                            <div className="product-name"><b>SKU : </b> {modified.addon_item_sku}</div>
                            {modified.addon_dimensions &&
                                <div className="dimensions"><b>Modified Dimensions : </b> {modified.addon_dimensions}</div>
                            }
                            {modified.addon_payment_extra_or_refund &&
                                <div className="dimensions"><b>Payment Type : </b> {modified.addon_payment_extra_or_refund}</div>
                            }
                            <div className="dimensions"><b>Modification Price : </b> {convertCurrency(modified.addon_item_price)}</div>
                            {modified.addon_payment_tracking_id && modified.addon_payment_type.toLowerCase() === "online" &&
                                <div className="dimensions"><b>{!isNaN(modified.addon_payment_source) ? getPaymentResourceById(modified.addon_payment_source).name : modified.addon_payment_source} Tracking ID : </b> {modified.addon_payment_tracking_id}</div>
                            }
                            <div className="dimensions"><b>Net Amount : </b> {convertCurrency(modified.addon_effective_price)}</div>
                            <div className="dimensions"><b>Extra / Refund Payment Mode : </b> {modified.addon_payment_type}</div>
                            {modified.addon_extra_payment_status && <div className="dimensions"><b>{modified.addon_payment_extra_or_refund} Status : </b> {modified.addon_extra_payment_status}</div>}
                            {modified.addon_reason && <div className="dimensions"><b>Modify Reason : </b> {modified.addon_reason}</div>}
                            {modified.addon_comments && <div className="sku"><b>Modification Comment : </b> {modified.addon_comments}</div>}
                        </div>
                    </div>
                })}
            </div>
        );
    }
}

export default ModificationHistory;
