import { GET_CALLDETAILS_SUCCESS, GET_CALLDETAILS_ERROR, RESET_CALLS, GET_CALLDETAILS_LOADING } from "./constants";
import { calldetails } from "../../api";
import { showLoader, hideLoader } from "../../components/Loader/action";
import handleError from "../../errorHandler";
import { toast } from "react-toastify";

export function getCallDetailsSuccess(data) {
    return {
        type: GET_CALLDETAILS_SUCCESS,
        data
    }
}

export function getCallDetailsLoading(data) {
    return {
        type: GET_CALLDETAILS_LOADING,
        data
    }
}

export function getCallDetailsError(data) {
    return {
        type: GET_CALLDETAILS_ERROR,
        data
    }
}

export function getAllCallDetails(callType, agentNumber, startDate, endDate, customerNumber, duration) {
    return (dispatch) => {
        dispatch(showLoader());
        dispatch(getCallDetailsLoading());
        calldetails(callType, agentNumber, startDate, endDate, customerNumber, duration)
            .then(response => {
                dispatch(hideLoader());
                // dispatch(getCallDetailsSuccess(response.data));
                if (response.status === 200) {
                    dispatch(getCallDetailsSuccess(response.data));
                } else if (response.status === 204) {
                    toast.info("No Data Available")
                }
            })
            .catch(error => {
                dispatch(getCallDetailsError(error.response));
                handleError(error, dispatch);
            });
    }
}

export function resetCalls() {
    return {
        type: RESET_CALLS
    }
}
