import { combineReducers } from 'redux';
import authReducer from '../containers/Login/reducer';
import headerReducer from '../components/Header/reducer';
import changePasswordReducer from '../containers/ChangePassword/reducer';
import orderSearchReducer from '../containers/OrderSearch/reducer';
import cetReducer from '../containers/Dashboard/reducer';
import ocReducer from '../containers/OcModule/reducer';
// import rtReducer from '../containers/RTModule/reducer';
import activityReducer from '../containers/Activity/reducer';
import returnsReplacementReducer from '../containers/ReturnsModule/reducer';
import callDetails from '../containers/TotalCalls/reducer';
import placeNewOrder from '../containers/PlaceNewOrder/reducer';
import stockManagementReducer from '../containers/StockManagement/reducer';
import importAffiliateReducer from '../containers/ImportSheets/reducer';
import ProductionOrders from '../containers/ProductionOrders/reducer';
import loaderReducer from '../components/Loader/reducer';
import popup from '../components/Popup/reducer';
import cartReducer from '../containers/CartEdit/reducer';
import LogisticReducer from '../containers/ImportLogisticSheet/reducer';
import SalesReducer from '../containers/SalesModule/reducer';
import ResetPasswordReducer from '../containers/ResetPassword/reducer';
import CreateMemberReducer from '../containers/CreateMember/reducer';
import ListCetReducer from '../containers/ListAllCets/reducer';
import cetActivityReducer from '../containers/CetActivityList/reducer';
import universalCallSearch from '../containers/UniversalCallSearch/reducer';
import reportsReducer from '../containers/Reports/reducer';
import financeReducer from '../containers/FinanceModule/reducer';
import codToOnlineReducer from '../containers/CodToOnline/reducer';
import MissindgItemsReducer from '../containers/MissingItems/reducer';
import ListCouponsReducer from '../containers/ListAllCoupons/reducer';
import CouponsDetailsReducer from '../containers/CouponOrderDetails/reducer';
import ProductReviewReducer from '../containers/ProductReviewAndQA/reducer';
import importSheetFinanceReducer from '../containers/UploadCODCollectionSheet/reducer';

const rootReducer = combineReducers({
    login: authReducer,
    userDetails: headerReducer,
    changePassword: changePasswordReducer,
    orderSearch: orderSearchReducer,
    ocData: ocReducer,
    general: cetReducer,
    activities: activityReducer,
    CartEditDetails: cartReducer,
    return: returnsReplacementReducer,
    callDetails: callDetails,
    placeNewOrder: placeNewOrder,
    stockManagement: stockManagementReducer,
    importAffiliateOrders: importAffiliateReducer,
    productionOrders: ProductionOrders,
    importLogistic: LogisticReducer,
    salesData: SalesReducer,
    resetPasswordData: ResetPasswordReducer,
    createMemberData: CreateMemberReducer,
    cetDetails: ListCetReducer,
    cetActivity: cetActivityReducer,
    universalCallSearch: universalCallSearch,
    reportDetails: reportsReducer,
    financeDetails: financeReducer,
    importSheetFinance: importSheetFinanceReducer,
    codToOnlineDetails: codToOnlineReducer,
    missingItemDetails: MissindgItemsReducer,
    couponsDetails: ListCouponsReducer,
    couponAndOrdersDetails: CouponsDetailsReducer,
    ProductReviewDetails:ProductReviewReducer,
    loader: loaderReducer,
    popup: popup
});

export default rootReducer;