import React, { Component } from 'react';
import './style.scss'
import { connect } from 'react-redux'
import { bindActionCreators, compose } from 'redux';
import withStyles from '@material-ui/core/styles/withStyles';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import { FormHelperText } from '@material-ui/core';
import InputLabel from '@material-ui/core/InputLabel';
import Button from '../../Button';
import { getStateCity, verifyGSTDetails, resetGSTData } from '../../../containers/PlaceNewOrder/action';
import { charsRegex, emailRegex, numberRegex, gstRegex } from '../../../config/regex';


const Styles = theme => ({
    form: {
        width: '100%', height: '100%',
        marginTop: theme.spacing.unit,
    }
});


class EditCustomerDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            fields: {
                firstname: "",
                lastname: "",
                email_address: "",
                mobile_number: "",
                address: "",
                landmark: "",
                city: "",
                state: "",
                pincode: "",
                alternate_mobile_number: "",
                gst_number: "",
                gst_organization: ""
            },
            errors: {},
            order_random_id: "",
            enableSubmit: false,
            affiliate_id: "",
            isValid: false
        }
    }

    componentWillMount() {
        let fields = {
            firstname: this.props.customerDetail.props.original.firstname,
            lastname: this.props.customerDetail.props.original.lastname,
            email_address: this.props.customerDetail.props.original.email_address,
            mobile_number: this.props.customerDetail.props.original.mobile_number,
            address: this.props.customerDetail.props.original.address,
            landmark: this.props.customerDetail.props.original.landmark,
            city: this.props.customerDetail.props.original.city,
            state: this.props.customerDetail.props.original.state,
            pincode: this.props.customerDetail.props.original.pincode,
            alternate_mobile_number: this.props.customerDetail.props.original.alternate_mobile_number,
            gst_number: this.props.customerDetail.props.original.gst_number,
            gst_organization: this.props.customerDetail.props.original.gst_organization
        };
        let order_random_id = this.props.customerDetail.props.original.order_random_id;
        let affiliate_id = this.props.customerDetail.props.original.affiliate_id

        this.setState({
            fields, order_random_id, affiliate_id
        })
    }

    componentWillReceiveProps(props) {
        let { fields, errors } = this.state;
        if (props.zipInfo && props.zipInfo.message) {
            errors.pincode = props.zipInfo.message;

            this.setState({ errors });

        } else if (props.zipInfo &&
            (fields.city !== props.zipInfo.city ||
                fields.state !== props.zipInfo.state)) {

            let cartDetails = props.customerDetail &&
                props.customerDetail.props &&
                props.customerDetail.props.original;

            fields.city = props.zipInfo.city;
            fields.state = props.zipInfo.state;

            errors.pincode = props.zipInfo.cod_avaibility === "NO" ?
                (cartDetails.payment_type &&
                    (['ONLINE', 'ECOM'].includes(cartDetails.payment_type.toLocaleUpperCase()))) ?
                    '' :
                    'COD is not available at given Pincode' :
                '';

            this.setState({ fields, errors });
        }
        if (props.gstInfo) {
            // let { fields, errors } = this.state;
            if (props.gstInfo.message) {
                errors.gst_number = props.gstInfo.message;
                fields["gst_organization"] = "";
                this.setState({ fields, errors },
                    // () => this.checkValidityOfForm()
                );
            } else if (props.gstInfo) {
                errors.gstNo = props.gstInfo.gstn_status && String(props.gstInfo.gstn_status).toLocaleLowerCase() === "active" ? "" : "Inactive/ Invalid GST Number";
                fields["gst_organization"] = props.gstInfo.legal_name_of_business;
                this.setState({ fields, errors })
            }
        }
    }

    handleInputChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;
        let { fields, errors } = this.state;

        switch (fieldName) {
            case 'firstname':
                if (!fieldValue) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "First Name is Required!";
                } else if (charsRegex.test(fieldValue) && fieldValue.length >= 3) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (charsRegex.test(fieldValue) && fieldValue.length < 3) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Minimum of 3 Characters are required!";
                }
                break;

            // case 'lastname':
            //     if (!fieldValue) {
            //         fields[fieldName] = fieldValue;
            //         errors[fieldName] = "Last Name is Required!";
            //     } else if (charsRegex.test(fieldValue) && fieldValue.length >= 3) {
            //         fields[fieldName] = fieldValue;
            //         errors[fieldName] = "";
            //     } else if (charsRegex.test(fieldValue) && fieldValue.length < 3) {
            //         fields[fieldName] = fieldValue;
            //         errors[fieldName] = "Minimum of 3 Characters are required!";
            //     }
            //     break;

            case 'email_address':
                if (!fieldValue) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Email is Required!";
                } else if (emailRegex.test(fieldValue)) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (!emailRegex.test(fieldValue)) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Invalid Email Id!";
                }
                break;

            case 'mobile_number':
                if (numberRegex.test(fieldValue) && fieldValue.length === 10) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (numberRegex.test(fieldValue) && fieldValue.length >= 1 && fieldValue.length < 10) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Invalid Mobile Number!";
                } else if (fieldValue.length === 0) {
                    fields[fieldName] = "";
                    errors[fieldName] = "Mobile Number Required!";
                }
                break;

            case 'address':
                if (fieldValue && fieldValue.length >= 5) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (fieldValue && fieldValue.length < 5) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Invalid Address!";
                } else if (fieldValue.length === 0) {
                    fields[fieldName] = "";
                    errors[fieldName] = "Address is Required!";
                }
                break;

            case "landmark":
                if (!fieldValue) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Landmark is Required!";
                } else if (fieldValue && fieldValue.length >= 3) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (fieldValue && fieldValue.length < 3) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Minimum of 3 Characters are required!";
                }
                break;

            case 'pincode':
                if (numberRegex.test(fieldValue) && fieldValue.length === 6) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                    // Get State and City
                    this.getStateCity(fieldValue);
                } else if (numberRegex.test(fieldValue) && fieldValue.length < 6) {
                    fields[fieldName] = fieldValue;
                    fields["city"] = "";
                    fields["state"] = "";
                    errors[fieldName] = "Invalid Pin Code!";
                } else if (fieldValue.length === 0) {
                    fields[fieldName] = "";
                    errors[fieldName] = "Pin Code is Required!";
                }
                break;

            case 'gst_number':
                let gst = fieldValue.toLocaleUpperCase();
                if (gst.length === 0) {
                    fields[fieldName] = "";
                    fields["gst_organization"] = "";
                    errors[fieldName] = "";
                    this.props.resetGSTData();
                }
                else if (gstRegex.test(gst) && gst.length === 15) {
                    fields[fieldName] = gst;
                    errors[fieldName] = "";
                    this.verifyGSTDetails(gst);
                }
                else if (!gstRegex.test(gst)) {
                    fields[fieldName] = gst;
                    fields["gst_organization"] = "";
                    this.props.resetGSTData();
                    errors[fieldName] = "Invalid GST number!";
                }
                break;

            case 'alternate_mobile_number':
                if (numberRegex.test(fieldValue) && fieldValue.length === 10) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (numberRegex.test(fieldValue) && fieldValue.length >= 1 && fieldValue.length < 10) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Invalid Alternate Mobile Number!";
                } else if (fieldValue.length === 0) {
                    fields[fieldName] = "";
                    errors[fieldName] = "";
                    // errors[fieldName] = "Alternate Mobile Number Required!";
                }
                break;

            default: fields[fieldName] = fieldValue;
                break;
        }
        this.setState({ fields, errors }, () => {
            // this.checkValidityOfForm()
        });
    }

    checkValidityOfForm = (event) => {
        event.preventDefault();
        let { fields, errors } = this.state;

        let fieldNames = {
            "firstname": "Firstname",
            // "lastname": "Lastname", 
            "mobile_number": "Mobile Number",
            "email_address": "Email",
            "address": "Address",
            "landmark": "Landmark",
            "pincode": "Pincode",
            // "city": "City",
            // "state": "State"
        }

        Object.keys(fieldNames).map(key => {
            if (!fields[key]) {
                errors[key] = errors[key] !== '' ? `${fieldNames[key]} is Required!` : errors[key];
            }
            return "";
        });

        let hasErrors = Object.keys(errors).length !== 0 &&
            Object.keys(fieldNames)
                .some(field => field !== 'pincode' && errors[field] && errors[field] !== "");

        this.setState({
            fields,
            errors,
            isValid: !hasErrors
        }, () => {
            let { isValid } = this.state;
            console.log(this.state);
            // hasErrors ? errors.pincode !== 'Service not available' : 
            isValid && this.handleSubmit();
        });
    }

    getStateCity = (zipCode) => {
        let data = { "pincode": zipCode };
        this.props.getStateCity(data);
    }

    verifyGSTDetails = (gstNo) => {
        // console.log(gstNo)
        let data = { "gst_number": gstNo };
        this.props.verifyGSTDetails(data);
    }

    handleSubmit = (event) => {
        let { fields, order_random_id, affiliate_id } = this.state;

        this.props.userDetails &&
            this.props.userDetails.role &&
            this.props.onSubmit(
                // this.props.userDetails.role.includes("OC") ? "OC" :
                // this.props.userDetails.role.includes("OPS") ? "OPS" : "", 
                "",
                {
                    "user_data": fields,
                    "order_random_id": order_random_id,
                    "affiliate_id": affiliate_id
                })
    }

    render() {
        let { classes } = this.props;
        let { fields, errors } = this.state;
        return (
            <div className="customerEdit">
                <form className={classes.form} noValidate autoComplete="off">
                    <div className="form-container-customer-edit">
                        <FormControl className="form-field-container" margin="normal" error={errors.firstname} >
                            <InputLabel htmlFor="text">First Name</InputLabel>
                            <Input
                                id="firstname"
                                type="text"
                                name="firstname"
                                placeholder="Firstname"
                                value={fields.firstname}
                                onChange={this.handleInputChange}
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.firstname}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" error={errors.lastname} >
                            <InputLabel htmlFor="text">Last Name</InputLabel>
                            <Input
                                id="lastname"
                                type="text"
                                name="lastname"
                                placeholder="Lastname"
                                value={fields.lastname}
                                onChange={this.handleInputChange}
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.lastname}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" error={errors.email_address} >
                            <InputLabel htmlFor="text">Email</InputLabel>
                            <Input
                                id="email_address"
                                type="email"
                                name="email_address"
                                placeholder="Email"
                                value={fields.email_address}
                                onChange={this.handleInputChange}
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.email_address}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" error={errors.mobile_number} >
                            <InputLabel htmlFor="text">Mobile Number</InputLabel>
                            <Input
                                id="mobile_number"
                                type="text"
                                name="mobile_number"
                                placeholder="Mobile No"
                                value={fields.mobile_number}
                                onChange={this.handleInputChange}
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.mobile_number}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container address-field" margin="normal" error={errors.address} >
                            <InputLabel htmlFor="text">Address</InputLabel>
                            <Input
                                rows={3}
                                multiline={true}
                                id="address"
                                type="text"
                                name="address"
                                placeholder="Address"
                                value={fields.address}
                                onChange={this.handleInputChange}
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.address}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" error={errors.landmark} >
                            <InputLabel htmlFor="text">Landmark*</InputLabel>
                            <Input
                                id="landmark"
                                type="text"
                                name="landmark"
                                placeholder="Landmark"
                                value={fields.landmark}
                                onChange={this.handleInputChange}
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.landmark}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" error={errors.pincode} >
                            <InputLabel htmlFor="text">Pin Code</InputLabel>
                            <Input
                                id="pincode"
                                type="text"
                                name="pincode"
                                placeholder="Pincode"
                                value={fields.pincode}
                                onChange={this.handleInputChange}
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.pincode}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" error={errors.city}>
                            <InputLabel htmlFor="text">City</InputLabel>
                            <Input
                                id="city"
                                type="text"
                                name="city"
                                placeholder="City"
                                disabled={true}
                                value={fields.city}
                            // onChange={this.handleInputChange} 
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.city}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" error={errors.state}>
                            <InputLabel htmlFor="text">State</InputLabel>
                            <Input
                                id="state"
                                type="text"
                                name="state"
                                placeholder="State"
                                disabled={true}
                                value={fields.state}
                            // onChange={this.handleInputChange} 
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.state}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" error={errors.alternate_mobile_number} >
                            <InputLabel htmlFor="text">Mobile Number[A]</InputLabel>
                            <Input
                                id="alternate_mobile_number"
                                type="text"
                                name="alternate_mobile_number"
                                placeholder="Alternate mobile number"
                                value={fields.alternate_mobile_number}
                                onChange={this.handleInputChange}
                            />
                            <FormHelperText className="error-text" id="email-text">{errors.alternate_mobile_number}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" error={errors.gst_number} >
                            <InputLabel htmlFor="text">GST Number</InputLabel>
                            <Input
                                id="gst_number"
                                type="text"
                                name="gst_number"
                                placeholder="GST Number"
                                value={fields.gst_number}
                                onChange={this.handleInputChange}
                            />
                            <FormHelperText className="error-text" id="gst-text">{errors.gst_number}</FormHelperText>
                        </FormControl>

                        <FormControl className="form-field-container" margin="normal" >
                            <InputLabel htmlFor="text">GST Organization</InputLabel>
                            <Input
                                id="gst_organization"
                                type="text"
                                name="gst_organization"
                                disabled={true}
                                placeholder="GST Organization"
                                value={fields.gst_organization}
                                onChange={this.handleInputChange}
                            />
                        </FormControl>

                    </div>
                    <Button
                        className="update-btn"
                        label="Update"
                        // disabled={!this.state.enableSubmit}
                        onClick={this.checkValidityOfForm}
                    />
                </form>
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        userDetails: state.userDetails,
        zipInfo: state.placeNewOrder.zipInfo,
        gstInfo: state.placeNewOrder.gstInfo,
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        getStateCity,
        verifyGSTDetails,
        resetGSTData
    }, dispatch)
}

export default compose(
    withStyles(Styles),
    // withWidth(Styles),
    connect(mapStateToProps, mapDispatchToProps),
)(EditCustomerDetail);
