import { GET_CALLDETAILS_SUCCESS, GET_CALLDETAILS_ERROR, RESET_CALLS, GET_CALLDETAILS_LOADING } from './constants';

const initialState = {
    calls: [],
    loading: false
}

export default function orderSearchReducer(state = initialState, action) {
    switch (action.type) {
        case GET_CALLDETAILS_SUCCESS:
            return Object.assign({}, state, {
                calls: Array.isArray(action.data) ? action.data : [],
                loading: false
            });

        case GET_CALLDETAILS_LOADING:
            return Object.assign({}, state, {
                calls: [],
                loading: true
            });

        case GET_CALLDETAILS_ERROR:
            return Object.assign({}, state, {
                error: action.data,
                calls: [],
                loading: false
            });

        case RESET_CALLS:
            return Object.assign({}, state, {
                calls: [],
                loading: false
            });

        default:
            return state;
    }
}