import { getUserdetails } from "../../api";
import { GET_USER_DETAILS_SUCCESS, GET_USER_DETAILS_ERROR, RESET_USER_DETAILS } from './constants';
// import { logoutAction } from '../../containers/Login/action';
import { hideLoader, showLoader } from "../Loader/action";
import handleError from "../../errorHandler";

export function getUserDetailsAction(thisProps) {
    return (dispatch) => {
        dispatch(showLoader());
        getUserdetails()
            .then(response => {
                if (response.status === 200) {
                    dispatch(hideLoader());
                    dispatch(getUserSuccess(response.data));
                }
            })
            .catch(error => {
                handleError(error, dispatch);
                // dispatch(hideLoader());
                // if (error.response && error.response.status === 401 && error.response.data.detail === "Token is expired.") {
                //     dispatch(logoutAction(thisProps));
                //     dispatch(resetUserdetails());
                //     dispatch(hideLoader());
                // } else {
                //     dispatch(hideLoader());
                //     dispatch(getUserError(error.response));
                // }
            });
    };
}

export function getUserSuccess(data) {
    return {
        type: GET_USER_DETAILS_SUCCESS,
        data
    }
}

export function getUserError(data) {
    return {
        type: GET_USER_DETAILS_ERROR,
        data
    }
}

export function resetUserdetails() {
    return {
        type: RESET_USER_DETAILS
    }
}