import { CREATE_MEMBER_SUCCESS, RESET_CREATE_MEMBER } from "./constants";

const initialState = {
    response: null,
    resetData: false
};

export default function createMember(state = initialState, action) {
    switch (action.type) {
        case CREATE_MEMBER_SUCCESS:
            return Object.assign({}, state, {
                response: action.data,
                resetData: false
            });

        case RESET_CREATE_MEMBER:
            return Object.assign({}, state, {
                response: null,
                resetData: false
            });

        default: return state;
    }
}