import { hideLoader } from "../components/Loader/action";
import { logoutSuccess } from "../containers/Login/action";
import { resetUserdetails } from "../components/Header/action";
import { toast } from "react-toastify";
import { hidePopup } from "../components/Popup/action";

const handleError = (error, dispatch) => {
    let errorMessage = "";
    let warning = "";

    console.log(error);
    dispatch && dispatch(hideLoader());
    // Error
    if (error.response) {
        // The request was made and the server responded with a status code
        // that falls out of the range of 2xx
        // console.log(error.response.data);
        // console.log(error.response.status);
        // console.log(error.response.headers);
        console.log('Response Error', error.response);
        console.log('Response Error Code', error.response.status);
        if ((error.response.status === 401 &&
            (
                (error.response.data.detail &&
                    (error.response.data.detail.toLowerCase("token is expired") ||
                        error.response.data.detail.toLowerCase("token is not valid")
                    ))
                || (
                    error.response.data.error &&
                    error.response.data.error.toLowerCase("token is expired.")
                )
            )

        ) ||

            (error.response.status === 403 &&
                error.response.data.error &&
                error.response.data.error.toLowerCase().includes("invalid token"))
        ) {
            dispatch(hidePopup());
            dispatch(logoutSuccess());
            dispatch(resetUserdetails());
            errorMessage = error.response.data.detail || error.response.data.error;
        }
        // else if (error.response.status === 404) errorMessage = "API Not Found";
        else if (error.response.data.error) errorMessage = error.response.data.error;
        else if (Array.isArray(error.response.data.message) && error.response.data.message.length > 0) errorMessage = error.response.data.message[0];
        else if (error.response.data.message) errorMessage = error.response.data.message;
        else if (error.response.statusText) errorMessage = error.response.statusText;
        else if (error.response.status === 404) errorMessage = "API Not Found";

    } else if (error.request) {
        // The request was made but no response was received
        // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
        // http.ClientRequest in node.js
        if (error.request.data && error.request.data.error) errorMessage = error.request.data.error;
        else if (error.request.data && error.request.data.message) errorMessage = error.request.data.message;
        else if (error.request.statusText) errorMessage = error.request.statusText;
        else if (error.request.status === 404) errorMessage = "API Not Found";

        console.log('Request Error', error.request);
    } else {
        // Something happened in setting up the request that triggered an Error
        console.log('Error', error.message);
    }
    console.log(error.config);
    errorMessage && toast.error(errorMessage);
    warning && toast.warn(warning);
    return "";
}

export default handleError;