import React, { Component } from 'react';
import './style.scss';

import { getDateTime } from '../../../config/time';
import { getOrderStatusById } from '../../../config/orderStatus';

class Comments extends Component {
    render() {
        return (
            <div className="comments-container">
                {Array.isArray(this.props.comments) && this.props.comments.length > 0 ? this.props.comments.map((comment, index) => {
                    let orderStatusText = getOrderStatusById(comment.order_status).label;
                    return <div className="comments-content" key={`comment-${index}`}>
                        <div className="comments-header"><div className="status">{orderStatusText}</div><div>{getDateTime(comment.update_timestamp)}</div></div>
                        <div className="comments-body break-word">{comment.status_comments}</div>
                        <div className="comments-footer">{comment.update_by_role} - {comment.cet_name}</div>
                    </div>
                }) : this.props.comments.length === 0 && <div>No Comments...</div>}
            </div>
        );
    }
}

export default Comments;