import { login, logout } from '../../api';
import { AxiosAPI } from '../../api/axiosApi';
import { getUserDetailsAction, resetUserdetails } from '../../components/Header/action';
import { showLoader, hideLoader } from '../../components/Loader/action';

import { LOGIN_SUCCESS, LOGOUT_SUCCESS, LOGOUT_ERROR, TOGGLE_HOMEREDIRECT } from './constants';

import errorHandler from '../../errorHandler';
import { toast } from 'react-toastify';
import { generateCookie, getCookieInString } from '../../utilities/cookies';

export function loginAction(data, thisProps) {
    return (dispatch) => {
        dispatch(showLoader());
        login(data)
            .then(response => {
                if (response.status === 200) {
                    new Promise((resolve) => resolve(dispatch(loginSuccess(response.data)))).then((response) => {
                        generateCookie(data);
                        AxiosAPI.defaults.headers = {
                            'Authorization': 'Bearer ' + localStorage.getItem('authToken'),
                            'Api-Token': process.env.REACT_APP_API_TOKEN,
                            'Api-Secret-Key': process.env.REACT_APP_API_SECRET_KEY,
                            'My-Cookie': getCookieInString(),       //  gets JSON.stringify of cookie content
                        }
                        console.log("Success");
                        dispatch(showLoader());
                        dispatch(getUserDetailsAction(thisProps));
                        // toast.success("Login Succsessfull!");
                        // dispatch(loginSuccess(response.data));
                    });
                } else {
                    dispatch(hideLoader());
                }
            })
            .catch(error => {
                errorHandler(error, dispatch);
            });
    }
}

export function loginSuccess(data) {
    return {
        type: LOGIN_SUCCESS,
        data
    }
}

export function logoutAction() {
    return (dispatch) => {
        dispatch(showLoader());
        logout()
            .then((response) => {
                if (response.status === 200) {
                    dispatch(resetUserdetails());
                    dispatch(hideLoader());
                    dispatch(logoutSuccess());
                    toast.success(response.data.message);
                }
            })
            .catch((error) => {
                errorHandler(error, dispatch);
            });
    }
}

export function logoutSuccess() {
    return {
        type: LOGOUT_SUCCESS,
    }
}

export function toggleRedirectHome(toggle) {
    return {
        type: TOGGLE_HOMEREDIRECT,
        toggle
    }
}

export function logoutError(data) {
    return {
        type: LOGOUT_ERROR,
        data
    }
}
