import moment from 'moment';

export const getTodayDate = () => {
    return moment.utc(Date.now()).format("YYYY-MM-DD");
}

export const getAfterDate = (today, numberOfDaysToAdd = 0) => {
    return moment(today, "YYYY-MM-DD").add("days", numberOfDaysToAdd).format("YYYY-MM-DD");
}

export const getDateTime = (timestamp = "", type = "") => {                                 //  type in ["now"]
    const date = type === "" ?
        timestamp && new moment.utc(timestamp).format("D MMM, YYYY h:mm A") :               //  return converted time.
        type === "now" && new moment().utcOffset("+05:30").format("D MMM, YYYY h:mm A");    //  return current IST time of now.
    return date || "";
}


export const getDate = (timestamp) => {
    const date = timestamp && new moment.utc(timestamp).format("D MMM, YYYY");
    return date;
}

export const getTime = (timestamp) => {
    const date = timestamp && new moment.utc(timestamp).format("hh:mm:ss A");
    return date;
}

export const getNumberOfDays = (timestamp) => {
    if (timestamp) {
        let today = Date.now();

        var date2 = new Date(today);
        var date1 = new Date(timestamp);
        var timeDiff = Math.abs(date2.getTime() - date1.getTime());
        // let dayDifference = Math.ceil(timeDiff / (1000 * 3600 * 24));
        let dayDifference = Math.floor(timeDiff / (1000 * 3600 * 24));

        // timestamp && console.log("Timestamp", date1, date2, timeDiff / (1000 * 3600 * 24), Math.floor(timeDiff / (1000 * 3600 * 24)), dayDifference);
        return dayDifference;
    } else {
        return 0;
    }
}
