import React, { Component } from "react";
import "./style.scss";
import Button from "../../Button";

class RefundPopup extends Component {
    render() {
        console.log(this.props);
        let { rowInfo } = this.props.data.popup;
        return (
            <div className="refund-popup-container">
                <div className="field-container">
                    <b>Refund Type</b>
                    <div>
                        <select className="custom-dropdown">
                            <option>Select Reason</option>
                        </select>
                    </div>
                </div>
                <div className="field-container">
                    <b>Amount</b>
                    <div>
                        <input
                            className="input-field"
                        />
                    </div>
                </div>
                <div className="field-container">
                    <b>Comments</b>
                    <div>
                        <textarea
                            className="input-field"
                            rows={4}
                        >
                        </textarea>
                    </div>
                </div>
                {rowInfo.payment_type && rowInfo.payment_type.toLowerCase().includes("cod") && <div>
                    <hr />
                    <div className="field-container"><b>Bank Details <span className="cod-text">(incase of COD)</span></b></div>
                    <div className="field-container">
                        <b>Account Holder Name</b>
                        <div>
                            <input
                                className="input-field" />
                        </div>
                    </div>
                    <div className="field-container">
                        <b>Account Number</b>
                        <div>
                            <input
                                className="input-field" />
                        </div>
                    </div>
                    <div className="field-container">
                        <b>IFSC Code</b>
                        <div>
                            <input
                                className="input-field" />
                        </div>
                    </div>
                    <div className="field-container">
                        <b>Bank Name</b>
                        <div>
                            <input
                                className="input-field" />
                        </div>
                    </div>
                </div>}
                <hr />
                <div className="field-container button-container">
                    <Button
                        label={"Initiate Refund"}
                    />
                </div>
            </div>
        );
    }
}

export default RefundPopup;
