// import initialState from './initialState';
import { GET_USER_DETAILS_SUCCESS, GET_USER_DETAILS_ERROR, RESET_USER_DETAILS } from './constants';

export default function headerReducer(state = {}, action) {
    switch (action.type) {
        case GET_USER_DETAILS_SUCCESS:
            return Object.assign({}, state, {
                ...action.data,
            });

        case "TOGGLE_USER_STATUS_SUCCESS":
            return Object.assign({}, state, {
                status: action.data.action_type === 2 ? "working" : "break"
            });

        case GET_USER_DETAILS_ERROR:
            return Object.assign({}, {}, {});

        case RESET_USER_DETAILS:
            return Object.assign({}, {}, {});

        default:
            return state;
    }
}