import {
    GET_PLACE_NEWORDER_PRODUCTS_SUCCESS,
    GET_STATE_CITY_SUCCESS,
    PLACE_NEWORDER_SUCCESS,
    REPRESE_PLACE_NEW_ORDER,
    RESET_STATE_CITY,
    RESET_PLACE_NEWORDER_PRODUCTS_SUCCESS,
    VERIFY_GST_DATA_SUCCESS,
    RESET_GST_DATA,
    GETTING_PRODUCT_DETAILS,
    GET_PRODUCT_DETAILS_SUCCESS,
    GET_PRODUCT_DETAILS_ERROR,
    PLACE_NEWORDER_ERROR,
    RESET_PLACE_NEWORDER_SUCCESS_AND_ERROR,
    RESET_PRODUCT_DETAILS_DATA
} from './constants';

const initialState = {
    response: null,
    placeOrderError: null
}

export default function orderSearchReducer(state = initialState, action) {
    switch (action.type) {

        case GET_PLACE_NEWORDER_PRODUCTS_SUCCESS:
            return {
                ...state,
                productDetails: action.data
            }

        case RESET_PLACE_NEWORDER_PRODUCTS_SUCCESS:
            return {
                ...state,
                productDetails: null
            }

        case GET_STATE_CITY_SUCCESS:
            return {
                ...state,
                zipInfo: action.data
            }

        case RESET_STATE_CITY:
            return {
                ...state,
                zipInfo: null
            }

        case PLACE_NEWORDER_SUCCESS:
            return Object.assign({}, state, {
                response: action.data,
                placeOrderError: null,
                resetData: action.data.resetData
            });

        case PLACE_NEWORDER_ERROR:
            return Object.assign({}, state, {
                placeOrderError: action.error
            });

        case RESET_PLACE_NEWORDER_SUCCESS_AND_ERROR:
            return Object.assign({}, state, {
                response: null,
                placeOrderError: null
            });

        case REPRESE_PLACE_NEW_ORDER:
            return {
                response: null,
                resetData: action.data
            }

        case VERIFY_GST_DATA_SUCCESS:
            return {
                ...state,
                gstInfo: action.data
            }

        case RESET_GST_DATA:
            return {
                ...state,
                gstInfo: null
            }

        case GETTING_PRODUCT_DETAILS:
            return Object.assign({}, state, {
                gettingProductData: true,
                productData: null,
                productError: null
            });

        case GET_PRODUCT_DETAILS_SUCCESS:
            return Object.assign({}, state, {
                gettingProductData: false,
                productData: action.data,
                productError: null
            });

        case GET_PRODUCT_DETAILS_ERROR:
            return Object.assign({}, state, {
                gettingProductData: false,
                productData: null,
                productError: action.error
            });

        case RESET_PRODUCT_DETAILS_DATA:
            return {
                ...state,
                productData: null,
                productError: null
            }

        default:
            return state;
    }
}