import React, { Component } from 'react';
import './style.scss';
import { getDate } from '../../../config/time';
import Button from '../../Button';
import { getShippingPartnerId } from '../../../config/ShippingPartner';

class index extends Component {

    TracktableHeader = () => {
        const header = ["#", "Discription", "Date", "Location"]
        return header.map((key, index) => {
            return <th className="tableHeader" key={index}>{key.toUpperCase()}</th>
        })
    }

    Tracktable = (tableData) => {
        return tableData.map((rowInfo, index) => {
            const { ScanDescription, ScanDate, ScanLocation } = rowInfo //destructuring

            return (
                <tr key={index}>
                    <td>{index + 1}</td>
                    <td>{ScanDescription}</td>
                    <td>{ScanDate}</td>
                    <td>{ScanLocation}</td>
                </tr>
            )
        })
    }

    getLinkToRedirect = (shippingPartnerName, docket_number) => {
        if (shippingPartnerName === "bluedart") {
            return `http://bluedart.in/?${docket_number}`;
        } else if (shippingPartnerName === "fedex") {
            return `https://www.fedex.com/apps/fedextrack/index.html?action=track&tracknumbers=${docket_number}&locale=en_US&cntry_code=en`;
        } else if (shippingPartnerName === "cityxfer") {
            return "https://www.cityxfer.com/trackus/";
        } else if (shippingPartnerName === "dtdc") {
            return "https://www.dtdc.in/tracking/shipment-tracking.asp";

        }
    }

    render() {
        let data = this.props.trackDetails && this.props.trackDetails;
        console.log("trackdata", data)
        let shippingPartnerName = data.payload && data.payload.shipping_id && getShippingPartnerId(data.payload.shipping_id).partnerName;

        return ((data && data.hasOwnProperty("error")) ?
            <div className="track-details">
                {shippingPartnerName &&
                    <a
                        href={this.getLinkToRedirect(shippingPartnerName.toLowerCase(), data.payload.docket_number)}
                        target="_blank"
                        without rel="noopener noreferrer"
                    >
                        <Button
                            className="track-btn"
                            label={`Track On ${shippingPartnerName}`} />
                    </a>}
                <div style={{ paddingTop: "20px" }} >
                    Something went wrong. Please click the above button.
                </div>
            </div>
            :
            <div className="track-details">
                {/* button add here for track through site*/}
                {data.docket_name &&
                    <a
                        href={this.getLinkToRedirect(data.docket_name.toLowerCase(), data.DocketNumber)}
                        target="_blank"
                        without rel="noopener noreferrer"
                    >
                        <Button
                            className="track-btn btn-right"
                            label={`Track On ${data.docket_name}`} />
                    </a>
                }
                <div className="track-container">
                    <div className="status"><label>Status: </label>{data.Status}</div>
                    <div className="EDD"><label>EDD: </label><span>{getDate(data.EstimatedDeliveryDate)}</span></div>
                    <div className="track-main">
                        <div className="row">
                            <div className="awb"><label>Invoice Number: </label><span>{data.InvoiceNumber}</span></div>
                            <div className="awb"><label>AWB: </label><span>{data.DocketNumber}</span></div>
                        </div>
                        <div className="row">
                            <div className="awb"><label>Origin: </label><span>{data.Origin}</span></div>
                            <div className="awb"><label>Destination: </label><span>{data.Destination}</span></div>
                        </div>
                    </div>
                </div>
                {/* <div className="track-table"> */}
                <table className="track-table">
                    <tbody>
                        <tr>{this.TracktableHeader()}</tr>
                        {this.Tracktable(data.Scans)}
                    </tbody>
                </table>
            </div>
        );
    }
}

export default index;