import {
    GET_PAYMENT_TPYE_UPDATE_DETAILS,
    RESET_UPDATED_PAYMENT_TYPE_DETAILS,
    UPDATE_PAYMENT_TYPE_SUCCSESS
} from "./constants";

const initialState = {
    paymentTypeDetails: null,
    refreshPaymentTypeDetails: false
};

export default function codToOnlineReducer(state = initialState, action) {
    switch (action.type) {
        case GET_PAYMENT_TPYE_UPDATE_DETAILS:
            return Object.assign({}, state, {
                paymentTypeDetails: action.data,
                refreshPaymentTypeDetails: false
            });

        case RESET_UPDATED_PAYMENT_TYPE_DETAILS:
            return Object.assign({}, state, {
                paymentTypeDetails: null
            });

        case UPDATE_PAYMENT_TYPE_SUCCSESS:
            return Object.assign({}, state, {
                refreshPaymentTypeDetails: true
            });

        default: return state;
    }
}