export const getPaymentTypes = () => {
    return paymentTypes;
}

export const getPaymentResources = () => {
    return paymentResources;
}

export const getPaymentResourcesByPaymentType = (paymentType = '') => {
    let paymentResourcesToShow = paymentResources
        .filter(resource =>
            String(resource.payment_type).toLocaleUpperCase() === String(paymentType).toLocaleUpperCase());
    return paymentResourcesToShow;
}

export const getPaymentResourcesToShow = (place = '', paymentType = 'ONLINE') => {
    let paymentResourcesToShow = paymentResources
        .filter(resource =>
            String(resource.payment_type).toLocaleUpperCase() === String(paymentType).toLocaleUpperCase() &&
            resource.showAt.includes(place));
    return paymentResourcesToShow;
}

export const getPaymentResourceById = (id) => {
    let resource = paymentResources.filter(resource => resource.id === id);
    resource = resource.length > 0 ? resource[0] : {};
    return resource;
}