export function sendInvoice(email, affiliateId, cartId, to = 1) {
    return (dispatch) => {
        let data = {
            "cet_email": email,
            "affiliate_id": affiliateId,
            "cart_pri_id": cartId,
            "to": to        // 1 for CET only 2 for Both
        }
        dispatch(showLoader());
        sendInvoiceTo(data)
            .then(response => {
                dispatch(hideLoader());
                toast.success("Invoice Sent Successfully!");
                // dispatch(sendInvoiceSuccess(response.data));
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    };
}

