import React from 'react';
import PropTypes from 'prop-types';
import ReactTable from 'react-table';
import dateformat from 'dateformat';
import ReactAudioPlayer from 'react-audio-player';
import './style.scss';
import "react-table/react-table.css";

function audioPathFormatter(props) {
    return <div id={`audio-id-${props.index + 1}`}>
        <ReactAudioPlayer
            src={props.value}
            // autoPlay
            controls
        />
    </div>
    // return <div>{props.value}</div>
}

function callTimeFormatter(props) {
    let row = props.original;
    const date = new Date(row.call_date + ' ' + row.call_time)
    return <div>{dateformat(date, "dS mmm, yyyy h:MM TT")}</div>
}

function callTypeFormatter(props) {
    let row = props.original;
    const callType = row.Call_Type ? row.Call_Type : "Incoming";
    return <div>{callType}</div>
}

class TableTotalCalls extends React.Component {
    render() {
        const {
            callDetails,
            // activeTab
        } = this.props;
        // console.log(activeTab);

        return (
            <div className="react-table-container">
                <ReactTable
                    data={callDetails}
                    filterable
                    minRows={0}
                    NoDataComponent={() => null}
                    defaultFilterMethod={(filter, row) => {
                        // console.log(filter, row);
                        return String(row[filter.id]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())
                    }
                    }
                    columns={[{
                        Header: 'S.No',
                        id: 'row',
                        width: 50,
                        filterable: false,
                        Cell: (row) => <div>{row.index + 1}</div>
                    }, {
                        Header: 'Customer Details',
                        accessor: 'caller_number',
                        width: 130

                    }, {
                        Header: 'Agent Details',
                        accessor: 'Agent_number',
                        width: 130
                    }, {
                        Header: 'Caller Type',
                        accessor: 'Caller_Type',
                        width: 100,
                        Cell: callTypeFormatter
                    }, {
                        Header: 'Call Time',
                        accessor: 'call_time',
                        width: 150,
                        Cell: callTimeFormatter
                    }, {
                        Header: 'Call Status',
                        accessor: 'Call_status',
                        width: 120,
                        Cell: (props) => <div className="break-word">{props.value}</div>
                    }, {
                        Header: 'Call Transfer Status',
                        accessor: 'call_transfer_status',
                        width: 150
                    }, {
                        Header: 'Caller Duration',
                        accessor: 'caller_duration',
                        width: 110
                    }, {
                        Header: 'Conversation Duration',
                        width: 150,
                        accessor: 'conversation_duration'
                    }, {
                        Header: 'Recording Path',
                        accessor: 'call_recording_url',
                        width: 300,
                        Cell: audioPathFormatter
                    }]}
                />
            </div>
        );
    }
}

TableTotalCalls.defaultProps = {
}

TableTotalCalls.propTypes = {
    callDetails: PropTypes.array.isRequired
}

export default TableTotalCalls;
