import { GET_CET_SALES_ACTIVITY, RESET_CET_SALES_ACTIVITY } from "./constants";

const initialState = {
    cetActivity: null,
    resetData: false
};

export default function createMember(state = initialState, action) {
    switch (action.type) {
        case GET_CET_SALES_ACTIVITY:
            return Object.assign({}, state, {
                cetActivity: action.data,
                resetData: false
            });

        case RESET_CET_SALES_ACTIVITY:
            return Object.assign({}, state, {
                cetActivity: null,
                resetData: false
            });

        default: return state;
    }
}
