import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import './style.scss';
import ToggleUserStatus from '../ToggleUserStatus';

class PageHeader extends React.Component {
    render() {
        let { header, count, userDetails } = this.props;
        return (
            <div className="page-header">{header} {count !== 0 && count && `(${count})`} {userDetails &&
                userDetails.role &&
                (userDetails.role.includes("OC") ||
                    userDetails.role.includes("RT") ||
                    userDetails.role.includes("CET") ||
                    userDetails.role.includes("SALES")
                ) && <div className="toggle-switch-container"><ToggleUserStatus /></div>}</div>
        );
    }
}

PageHeader.defaultProps = {
    count: 0
}

PageHeader.propTypes = {
    header: PropTypes.string.isRequired,
    count: PropTypes.number
}

const mapStateToProps = (state, ownProps) => {
    return {
        userDetails: state.userDetails,
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({}, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(PageHeader);
