import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

let Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`;

let Container = styled.div`
    max-width: 1170px;
`;

let Header = styled.h2`
    color: #3d2277;
`;

let BreakText = styled.span`
    color: red;
    font-weight:500;
    text-transform: uppercase;
`;

let UnderlineText = styled.span`
    font-weight: 500;
    text-decoration: underline;
`;

const BreakComponent = ({ userDetails }) => <Wrapper>
    <Container>
        <Header>Hi, {userDetails.name}</Header>
        <p>You are on <BreakText>Break</BreakText>. To get CRM Access click the <UnderlineText>Working/ Break</UnderlineText> Button at the top.</p>
    </Container>
</Wrapper>

BreakComponent.defaultProps = {
    userDetails: {}
}

BreakComponent.propTypes = {
    userDetails: PropTypes.object.isRequired
}

export default BreakComponent;
