import { GET_ALL_COUPONS, EDIT_COUPON_SUCCESS, RESET_REFRESH_COUPONS } from './constants';
import { GET_COUPON_CATEGORIES_SUCCESS, CREATE_COUPON_SUCCESS, GET_COUPON_DETAILS_SUCCESS, TOGGLE_COUPON_REFRESH, RESET_COMPONENT_DATA, RESET_COUPON_VAR } from '../CreateCoupon/constants';

const initialState = {
    couponsList: null,
    couponData: null,
    couponCategories: null,
    refreshData: false,
    reloadComponentWithFreshData: false,
    resetCouponData: false,
    refreshCoupons: false
};

export default function ListCouponsReducer(state = initialState, action) {
    switch (action.type) {
        case GET_ALL_COUPONS:
            return Object.assign({}, state, {
                refreshCoupons: false,
                couponsList: action.data
            });

        case GET_COUPON_CATEGORIES_SUCCESS:
            return Object.assign({}, state, {
                couponCategories: action.data
            });

        case CREATE_COUPON_SUCCESS:
            return Object.assign({}, state, {
                successData: action.data
            });

        case EDIT_COUPON_SUCCESS:
            console.log("Update");
            return Object.assign({}, state, {
                refreshCoupons: true,
                // couponData: action.data
            });

        case RESET_REFRESH_COUPONS:
            console.log("Update");
            return Object.assign({}, state, {
                refreshCoupons: false,
                // couponData: action.data
            });

        case GET_COUPON_DETAILS_SUCCESS:
            return Object.assign({}, state, {
                reloadComponentWithFreshData: true,
                couponData: action.data
            });

        case RESET_COUPON_VAR:
            return Object.assign({}, state, {
                reloadComponentWithFreshData: true,
                couponData: action.data
            });

        case TOGGLE_COUPON_REFRESH:
            return Object.assign({}, state, {
                refreshData: action.data
            });

        case RESET_COMPONENT_DATA:
            return Object.assign({}, state, state);

        default: return state;
    }

}