import { GET_ACTIVITIES_DATA, RESET_ACTIVITIES_DATA } from './constants';

const initialState = {};

export default function authReducer(state = initialState, action) {
    switch (action.type) {
        case GET_ACTIVITIES_DATA:
            return Object.assign({}, state, {
                ...action.data
            });

        case RESET_ACTIVITIES_DATA:
            return Object.assign({}, {}, {});

        default: return state;
    }
}