import React from 'react';
import { getShipmentBlockedReason } from '../config/shipmentBlockReasons';

export const removeDot = (text = "") => {
    return String(text).split(".").join("");
}

export const removeComma = (number = "") => {
    return String(number).split(",").join("");
}

export const getLockedStatus = (cartData) => {
    if (cartData && cartData.is_locked === 1) {
        return (
            <div>
                <div className="break-word"><b>Cart Locked : <span className="text-red">Yes</span></b></div>
                <hr />
            </div>
        );
    }
    return "";
}

export const getShipmentStatus = (cartData) => {
    if (!cartData.shipment_timestamp && cartData.shipment_not_possible && cartData.shipment_not_possible !== 0) {
        return (
            <div>
                <div className="break-word"><b>Shipment Blocked : <span className="text-red">Yes</span></b></div>
                <div className="break-word"><b>Blocked Reason : </b> <span className="text-red">{getShipmentBlockedReason(cartData.shipment_stop_reason)}</span></div>
                <hr />
            </div>
        );
    }
    return "";
}

export const generateRandomString = () => {
    return Math.random()            // Generate random number, eg: 0.123456
        .toString(36)               // Convert  to base-36 : "0.4fzyo82mvyr"
        .slice(-8);                 // Cut off last 8 characters : "yo82mvyr"
}
