import { GET_CALLS_SUCCESS, RESET_CALLS } from "./constants";

const initialState = {
    calls: null,
    resetData: false
};

export default function universalCallSearch(state = initialState, action) {
    switch (action.type) {
        case GET_CALLS_SUCCESS:
            return Object.assign({}, state, {
                calls: action.data,
                resetData: false
            });

        case RESET_CALLS:
            return Object.assign({}, state, {
                calls: null,
                resetData: false
            });

        default: return state;
    }
}