import { toggleUserStatus } from "../../api";
import { showLoader, hideLoader } from "../Loader/action";
import handleError from "../../errorHandler";
import { toast } from "react-toastify";

const TOGGLE_USER_STATUS_SUCCESS = "TOGGLE_USER_STATUS_SUCCESS";

export function toggleUserStatusAction(data) {
    return (dispatch) => {
        dispatch(showLoader());
        toggleUserStatus(data)
            .then(response => {
                if (response.status === 200) {
                    dispatch(hideLoader());
                    // toast.success(response.data.message);
                    toast.success(data.action_type === 2 ? "Back to Work!" : "Going on Break!");
                    dispatch(toggleUserStatusSuccess(data));
                }
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    };
}

export function toggleUserStatusSuccess(data) {
    return {
        type: TOGGLE_USER_STATUS_SUCCESS,
        data
    }
}
