export const convertCurrency = (number = 0, countryCode = "IN", maximumFractionDigits = 2) => {
    number = String(number).split(",").join("");
    let numArray = String(number).split(".");
    if (numArray.length === 1) {
        maximumFractionDigits = 0;
    }
    return Number(number)
        .toLocaleString(
            "en-" + countryCode,
            { minimumFractionDigits: 0, maximumFractionDigits: maximumFractionDigits }
        );
}
