import { RESET_PASSWORD_SUCCESS, RESET_DATA_RESET_PW } from "./constants";

const initialState = {
    resetData: false
};

export default function resetPasswordReducer(state = initialState, action) {
    switch (action.type) {
        case RESET_PASSWORD_SUCCESS:
            return Object.assign({}, state, {
                resetData: true
            });

        case RESET_DATA_RESET_PW:
            return Object.assign({}, state, {
                resetData: false
            });

        default: return state;
    }
}