import React from 'react';
import Switch from "react-switch";
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { toggleUserStatusAction } from './action';

const SwitchContainer = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 150px;
`;

const Working = styled.div`
    color: green;
    font-weight:600;
    text-transform: uppercase;
    padding-left:10px;

`;

const Break = styled.div`
    color: red;
    font-weight:600;
    text-transform: uppercase;
    padding-right:10px;

`;

class ToggleUserStatus extends React.Component {
    state = { toggle: false }
    toggleUserStatus = () => {
        let { userDetails, toggleUserStatusAction } = this.props;
        let payload = {
            "action_type": userDetails.status && userDetails.status.toLowerCase() === "working" ? 1 :
                userDetails.status && userDetails.status.toLowerCase() === "break" ? 2 : "NA"
            // 0 => Stop, 1 => Break, 2 => Working
        };
        toggleUserStatusAction(payload);
    }

    render() {
        let { userDetails } = this.props;
        console.log(this.props);
        return (
            <SwitchContainer>
                <Break>Break</Break>
                <Switch
                    onChange={this.toggleUserStatus}
                    checked={userDetails.status && userDetails.status.toLowerCase() === "working"}
                />
                <Working>Working</Working>
            </SwitchContainer>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        userDetails: state.userDetails
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ toggleUserStatusAction }, dispatch)
}

ToggleUserStatus.defaultProps = {
    userDetails: {},
}

ToggleUserStatus.propTypes = {
    userDetails: PropTypes.object.isRequired,
    toggleUserStatusAction: PropTypes.func.isRequired
}

export default connect(mapStateToProps, mapDispatchToProps)(ToggleUserStatus);
