import {
    GET_OC_ORDERS_SUCCESS, GET_OC_ORDERS_ERROR,
    RESET_OC_ORDERS, MODIFIED_HISTORY_SUCCESS,
    UPDATE_URGENT_DELIVERY_STATUS_SUCCESS,
    UPDATE_URGENT_DELIVERY_STATUS_ERROR
} from './constants';

const initialState = {
    // ordersData: {},
    // orderIdSearch: false,
    refreshOrders: false,
    // lastCallConnected: {},
    // customerDetails: {},
    // lastCurrentStatusUpdated: {}
}
export default function ocReducer(state = initialState, action) {
    switch (action.type) {

        case GET_OC_ORDERS_SUCCESS:
            return Object.assign({}, state, {
                ...action.data
            });

        case GET_OC_ORDERS_ERROR:
            return Object.assign({}, {}, {});

        case RESET_OC_ORDERS:
            return Object.assign({}, {}, {});

        case MODIFIED_HISTORY_SUCCESS:
            return Object.assign({}, state, {
                modificationPopup: action.data
            })

        case UPDATE_URGENT_DELIVERY_STATUS_SUCCESS:
            return Object.assign({}, state, {
                ...action.data
            })

        case UPDATE_URGENT_DELIVERY_STATUS_ERROR:
            return Object.assign({}, state, {
                ...action.data,
                refreshOrders: true
            })

        default:
            return state;
    }
}