import Axios from "axios"
import { getCookieInString } from "../utilities/cookies";
import { apiURLOriginal } from "./axiosApi";

const getBaseURL = () => {
    return apiURLOriginal;
}

const getAuthToken = () => {
    return localStorage.getItem("authToken");
}

const checkAuthToken = () => {
    let authToken = getAuthToken();
    if (authToken) {
        Axios.defaults.headers.Authorization = 'Bearer ' + authToken;
    }
}

export const get = (url, params, headers) => {
    checkAuthToken();
    return Axios.get(getBaseURL() + 'api/' + url, {
        headers: {
            ...headers,
            common: {
                'Authorization': 'Bearer ' + localStorage.getItem("authToken") || "",
                'Api-Token': process.env.REACT_APP_API_TOKEN,
                'Api-Secret-Key': process.env.REACT_APP_API_SECRET_KEY,
                'My-Cookie': getCookieInString(),               //  gets JSON.stringify of cookie content
                // 'Access-Control-Allow-Origin': '*'
            }
        },
        params: {
            ...params
        }
    });
}

export const post = (url, body, params, headers) => {
    return Axios.post(getBaseURL() + 'api/' + url, body, {
        headers: {
            ...headers,
            common: {
                'Authorization': 'Bearer ' + localStorage.getItem("authToken") || "",
                'Api-Token': process.env.REACT_APP_API_TOKEN,
                'Api-Secret-Key': process.env.REACT_APP_API_SECRET_KEY,
                'My-Cookie': getCookieInString(),               //  gets JSON.stringify of cookie content
                // 'Access-Control-Allow-Origin': '*'
            }
        },
        params: {
            ...params
        }
    })
}

export const put = (url, params, headers, data) => {
    checkAuthToken();
    return Axios.put(getBaseURL() + 'api/' + url, {
        ...data
    }, {
        headers: {
            ...headers,
            common: {
                'Authorization': 'Bearer ' + localStorage.getItem("authToken") || "",
                'Api-Token': process.env.REACT_APP_API_TOKEN,
                'Api-Secret-Key': process.env.REACT_APP_API_SECRET_KEY,
                'My-Cookie': getCookieInString(),               //  gets JSON.stringify of cookie content
                // 'Access-Control-Allow-Origin': '*'
            }
        },
        params: {
            ...params
        }
    });

}