import { UPLOAD_AFFILIATE_SHEET_SUCCESS, RESET_AFFILIATE_SHEET_DETAILS, RESET_AFFILIATE_COMPOENENT_DETAILS } from "./constants";

const initialState = {
    successData: null,
    resetAffiliate: false
};

export default function dashboardReducer(state = initialState, action) {
    switch (action.type) {
        case UPLOAD_AFFILIATE_SHEET_SUCCESS:
            return Object.assign({}, state, {
                successData: action.data,
                resetAffiliate: true
            });

        case RESET_AFFILIATE_SHEET_DETAILS:
            return Object.assign({}, state, {
                resetAffiliate: false
            });

        case RESET_AFFILIATE_COMPOENENT_DETAILS:
            return Object.assign({}, state, { ...initialState });

        default: return state;
    }
}