import {
    GET_CART_MODIFICATION_DETAILS,
    GET_CART_DETAILS_ERROR,
    UPDATE_CART_DETAILS_ERROR,
    UPDATE_CART_DETAILS_SUCCESS,
    RESET_CART_DETAILS
} from './constants';

const initialState = {
    cartModificationData: null,
    refreshOrders: false,
}

export default function cartReducer(state = initialState, action) {
    switch (action.type) {

        case GET_CART_MODIFICATION_DETAILS:
            return Object.assign({}, state, {
                refreshOrders: false,
                cartModificationData: action.data
            });

        case "REFRESH_CART_EDIT_PAGE":
            return Object.assign({}, state, {});

        case GET_CART_DETAILS_ERROR:
            return Object.assign({}, state, {});

        case UPDATE_CART_DETAILS_SUCCESS:
            return Object.assign({}, state, {
                refreshOrders: true,
                ...action.data
            });

        case UPDATE_CART_DETAILS_ERROR:
            return Object.assign({}, state, {});

        case RESET_CART_DETAILS:
            return Object.assign({}, state, {
                cartModificationData: null,
                refreshOrders: false,
            });

        default:
            return state;
    }
}

