import { GET_STOCK_DETAILS, RESET_STOCK_DETAILS, UPLOAD_SUCCSESS } from "./constants";

const initialState = {
    stock: null,
    refreshStock: false
};

export default function dashboardReducer(state = initialState, action) {
    switch (action.type) {
        case GET_STOCK_DETAILS:
            return Object.assign({}, state, {
                stock: action.data,
                refreshStock: false
            });

        case RESET_STOCK_DETAILS:
            return Object.assign({}, state, {
                stock: null
            });

        case UPLOAD_SUCCSESS:
            return Object.assign({}, state, {
                refreshStock: true
            });

        default: return state;
    }
}