import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.scss';

class dashboard extends Component {

  render() {
    let details = (this.props.details ? this.props.details : []);
    console.log("Details ", details);
    return (
      <div className="dashboard-container">
        {details &&
          <p className="username">Hi {details.name}</p>
        }
        <h3>Welcome to</h3>
        <h2>Wakefit Backoffice</h2>
        {(details && localStorage.getItem('authToken') && (Object.keys(details).length > 0) ?
          <Link className="button pointer explore" to="/order-search">Explore CRM</Link> :
          <Link className="button pointer explore" to="/login">Login</Link>
        )}
      </div>
    );
  }
}

export default dashboard;