import React, { Component } from 'react';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import { hidePopup } from './action';
import './style.scss';

import { IconButton } from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import ModifiedHistory from './ModificationHistory';
import EditCustomerDetail from './EditCustomerDetail';
import TrackOrder from './TrackOrder';
import Comments from './Comments';
import ReturnReplacement from './ReturnReplacement';
import RefundPopup from './RefundPopup';
import CallRecordings from './CallRecordings';
import CetActivities from './CetActivities';
import Confirmation from './Confirmation';
import Button from '../Button';


class Popup extends Component {
    getPopup() {
        return <div className="popup-container">
            <div className="popup-background" onClick={this.props.hidePopup}></div>
            <div className="popup-content">
                <div className="popup-header">
                    <div className="popup-title">
                        {(this.props.popup.header) ||
                            "Popup Header"
                        }
                    </div>
                    <IconButton aria-label="Close" onClick={this.props.hidePopup}>
                        <CloseIcon />
                    </IconButton>
                </div>

                {this.props.popup.type === "confirmation" &&
                    <Confirmation data={this.props.popup.data} />
                }

                {this.props.popup.type === "commentsPerOrder" &&
                    this.props.popup.data &&
                    <div className="popup-body">
                        <Comments comments={this.props.popup.data} /></div>
                }
                {this.props.popup.type === "modifiedHistory" &&
                    this.props.popup.data &&
                    <div className="popup-body">
                        <ModifiedHistory modificationHistory={this.props.popup.data} /></div>
                }

                {this.props.popup.type === "customerEdit" &&
                    this.props.popup.data &&
                    <div className="popup-body">
                        <EditCustomerDetail customerDetail={this.props.popup.data} onSubmit={this.props.popup.data.onSubmit} />
                    </div>
                }

                {this.props.popup.type === "trackorderDetails" &&
                    this.props.popup.data &&
                    <div className="popup-body">
                        <TrackOrder trackDetails={this.props.popup.data} />
                    </div>
                }

                {(this.props.popup.type === "return" || this.props.popup.type === "replacement") &&
                    <ReturnReplacement data={this.props} />
                }

                {this.props.popup.type === "refund" &&
                    <RefundPopup data={this.props} />
                }

                {this.props.popup.type === "callRecordings" &&
                    <CallRecordings data={this.props.popup.data} />
                }

                {this.props.popup.type === "activityPopup" &&
                    <CetActivities data={this.props.popup.data} />
                }

                <div className="popup-footer">
                    {this.props.popup.data &&
                        this.props.popup.data.showSubmitBtn &&
                        <Button
                            label={this.props.popup.data.showSubmitBtnText || "Submit"}
                            onClick={this.props.popup.data.onSubmit}
                        />}
                    {this.props.popup.data &&
                        this.props.popup.data.showCancelBtn &&
                        <Button
                            label={this.props.popup.data.showCancelBtnText || "Cancel"}
                            onClick={() => {
                                this.props.hidePopup();
                                this.props.popup.data.onCancel &&
                                    this.props.popup.data.onCancel();
                            }}
                        />}
                </div>
            </div>
        </div>
    }
    render() {
        return this.props.popup.type ? this.getPopup() : <div></div>;
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
        popup: state.popup,
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ hidePopup }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(Popup);
