import { GET_REPORTS_SUCCESS, RESET_REPORTS } from "./constants";

const initialState = {
    reports: null,
    resetData: false
};

export default function universalCallSearch(state = initialState, action) {
    switch (action.type) {
        case GET_REPORTS_SUCCESS:
            return Object.assign({}, state, {
                reports: action.data.data,
                resetData: false
            });

        case RESET_REPORTS:
            return Object.assign({}, state, {
                reports: null,
                resetData: false
            });

        default: return state;
    }
}