import { CHANGE_PASSWORD_SUCCESS, TOGGLE_REFRESH_CHANGEPASSWORD } from './constants';

const initialState = {
    refreshChangePassword: false
}

export default function changePasswordReducer(state = initialState, action) {
    switch (action.type) {
        case CHANGE_PASSWORD_SUCCESS:
            return Object.assign({}, state, {});

        case TOGGLE_REFRESH_CHANGEPASSWORD:
            return Object.assign({}, state, {
                refreshChangePassword: action.data
            });

        default:
            return state;
    }
}