import {
    GET_RETURN_ORDERS, RESET_RETURNS_DATA, RETURN_REPLACEMENT_APPROVE,
    INTIATE_REFUND_SUCCESS, PACKAGING_SENT_SUCCESS, PICKUP_SCHEDULE_SUCCESS,
    ISSUE_ACCESSORY_SUCCESS, REPLACEMENT_CREATED, REFRESH_ORDERS, REPLACEMENT_MODIFIED
} from './constants';

const initialState = {
    ordersData: null,
    orderIdSearch: false,
    refreshOrders: false
}

export default function returnsReplacementReducer(state = initialState, action) {
    switch (action.type) {
        case GET_RETURN_ORDERS:
            return Object.assign({}, state, {
                ordersData: action.data,
                orderIdSearch: false,
                refreshOrders: false
            });

        case RESET_RETURNS_DATA:
            return Object.assign({}, state, {
                ordersData: null,
                orderIdSearch: false,
                refreshOrders: false
            });

        case RETURN_REPLACEMENT_APPROVE:
            return Object.assign({}, state, {
                // ordersData: {},
                orderIdSearch: false,
                refreshOrders: true
            });

        case PACKAGING_SENT_SUCCESS:
            return Object.assign({}, state, {
                // ordersData: {},
                orderIdSearch: false,
                refreshOrders: true
            });

        case PICKUP_SCHEDULE_SUCCESS:
            return Object.assign({}, state, {
                // ordersData: {},
                orderIdSearch: false,
                refreshOrders: true
            });

        case ISSUE_ACCESSORY_SUCCESS:
            return Object.assign({}, state, {
                // ordersData: {},
                orderIdSearch: false,
                refreshOrders: true
            });

        case REPLACEMENT_CREATED:
            return Object.assign({}, state, {
                // ordersData: {},
                orderIdSearch: false,
                refreshOrders: true
            });

        case REPLACEMENT_MODIFIED:
            return Object.assign({}, state, {
                // ordersData: {},
                orderIdSearch: false,
                refreshOrders: true
            });

        case INTIATE_REFUND_SUCCESS:
            return Object.assign({}, state, {
                // ordersData: null,
                orderIdSearch: false,
                refreshOrders: true
            });

        case REFRESH_ORDERS:
            return Object.assign({}, state, {
                refreshOrders: true
            });

        default:
            return state;
    }
}
